import React, { Component } from 'react'
import { Grid, Row, Col, Modal } from "react-bootstrap";

export default class Exploser extends Component {
    render() {
        return (
            <div className="boxFix">
                <Row>
                    <Col md={6}>
                        <div className="card ">

                            <div className="header">

                                <h6 className={'colorDarkBlue , loginCenter'}>Account - A</h6>
                            </div>
                            <div className="content table-full-width"><div className="table-responsive">
                                <table className="table">
                                    <tbody>

                                        <tr><td>Symbol </td>
                                            <td className="td-actions text-right">Symbol

                                            </td>
                                        </tr>
                                        <tr><td>Lots </td>
                                            <td className="td-actions text-right">Lots
                                            </td>
                                        </tr>
                                        <tr><td>BUY/SELL </td>
                                            <td className="td-actions text-right">Buy/Sell
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>Contract Size </td>
                                            <td className="td-actions text-right">Size
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>Exploser </td>
                                            <td className="td-actions text-right">Exploser Total
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            </div>
                        </div>
                    </Col>


                    <Col md={6}>
                        <div className="card ">

                            <div className="header">

                                <h6 className={'colorDarkBlue , loginCenter'}>Account - B</h6>
                            </div>
                            <div className="content table-full-width"><div className="table-responsive">
                                <table className="table">
                                    <tbody>

                                        <tr><td>Symbol </td>
                                            <td className="td-actions text-right">Symbol

                                            </td>
                                        </tr>
                                        <tr><td>Lots </td>
                                            <td className="td-actions text-right">Lots
                                            </td>
                                        </tr>
                                        <tr><td>BUY/SELL </td>
                                            <td className="td-actions text-right">Sell
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>Contract Size </td>
                                            <td className="td-actions text-right">Size
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>Exploser </td>
                                            <td className="td-actions text-right">Total
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            </div>
                        </div>
                    </Col>


                </Row>
            </div>

        )
    }
}
