import React, { Component } from "react";
import "react-calendar/dist/Calendar.css";
import "react-datepicker/dist/react-datepicker.css";
import { Grid, Row, Col, Button } from "react-bootstrap";
import config from "../../constant/config.js";
import "react-tagsinput/react-tagsinput.css";
import Select from "react-select";
import TagsInput from "react-tagsinput";

const axios = require("axios");

class UserEdit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      loggged_in: "",
      filter_profile: [],
      id: "",
      nameError: false,
      profiles: [],
      allChecked: false,
      filterdata: [],
      remove_filterdata: [],
    };
  }
  // componentDidMount() {

  // }
  componentDidMount() {
    this.allFilterprofiles();
  }

  allFilterprofiles() {
    let abc = this.props.data;

    let self = this;
    axios
      .get(config.baseurl + "/users/allFilterprofiles")
      .then(function (response) {
        if (response.status == 200) {
          let data = response.data.rows.map((item) => {
            return {
              ...item,
              checked: false,
            };
          });
          self.setState({ profiles: data }, () => {
            if (abc) {
              let selectedVal = self.state.profiles
                .filter(
                  (data) =>
                    JSON.parse(abc.filter_profile).includes(data.id) && true
                )
                .map((data) => {
                  return { value: data.id, label: data.profile_name };
                });
              self.setState({
                filter_profile: selectedVal,
                username: abc.username,
                loggged_in: abc.logged_in,
                id: abc.id,
              });
              // self.setState({
              //   username: abc.username,
              //   loggged_in: abc.logged_in,
              //   id: abc.id,
              // });
            }
          });
        }
      });
  }

  handleChangeFilterProfile = (filter_profile) => {
    this.setState({ filter_profile });
  };
  renderProfileSelect = () => {
    const { filter_profile, profiles } = this.state;
    let options = profiles.map((data) => {
      return { value: data.id, label: data.profile_name };
    });
    return (
      <Select
        placeholder="Select Profile"
        value={filter_profile}
        isMulti
        onChange={this.handleChangeFilterProfile}
        options={options}
      />
    );
  };
  handleusername(e) {
    this.setState({
      username:
        e.target.value.charAt(0).toUpperCase() + e.target.value.slice(1),
      error: "",
    });
  }
  formDataSubmit(e) {
    e.preventDefault();
    let self = this;
    let list = [];
    this.state.filter_profile && this.state.filter_profile.forEach((data) => {
      list.push(data.value);
    });
    let Data = {
      username: this.state.username,
      filter_profile: JSON.stringify(list),
      id: this.state.id,
    };

    axios
      .post(config.baseurl + `/users/update`, Data)
      .then(function (response) {
        if (response.status == 200) {
          self.props.onHide();
        }
      })
      .catch(function (error) {});
  }
  crossClick(e) {
    e.preventDefault();
    this.props.onHide();
  }

  render() {
    return (
      <div className="content">
        <Grid fluid>
          <div className="symbol-box">
            <div className="cross_div">
              <a onClick={(e) => this.crossClick(e)}>
                <i className="fa fa-times" aria-hidden="true"></i>
              </a>
            </div>
            <div className="detail-text">
              <h3> Edit User Details</h3>
            </div>
            <form>
              <div className="terminal-text">
                <p className="profilename pnameadjust"> Username</p>
                <input
                  type="text"
                  name="username"
                  value={this.state.username}
                  className="Itext"
                  onChange={(e) => this.handleusername(e)}
                />
              </div>
              <div className="terminal-text">
                <p className="profilename pnameadjust"> Profile Id's</p>

                {this.renderProfileSelect()}
              </div>
              <div className="terminal-text">
                <p className="profilename pnameadjust"> Logged In</p>
                <input
                  type="text"
                  name="loggedIn"
                  value={this.state.loggged_in}
                  className="Itext"
                  readOnly
                />
              </div>
              <div className="save_button_filter">
                <Button
                  className="btn-primary savebutton"
                  variant="primary"
                  size="lg"
                  block
                  onClick={(e) => this.formDataSubmit(e)}
                >
                  <b>Update</b>
                </Button>
              </div>
            </form>
          </div>
        </Grid>
      </div>
    );
  }
}

export default UserEdit;
