import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import { Dropdown, DropdownButton } from 'react-bootstrap'
import AdminNavbarLinks from "../Navbars/AdminNavbarLinks.jsx";

// import logo from "assets/img/reactlogo.png";

class Sidebar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth,

    };
  }
  toggle = (id) => {

    // console.log(this.props.routes)
    this.props.routes.filter((data) => {
      if (data.id == id) {
        data.menu = !data.menu
      }
      else {
        data.menu = false
      }
    })
  }

  activeRoute(routeName) {
    return this.props.location.pathname.indexOf(routeName) > -1 ? "active" : "";
  }
  updateDimensions() {
    this.setState({ width: window.innerWidth });
  }
  componentDidMount() {
    // console.log(this.props, 'sidebar');
    this.updateDimensions();
    window.addEventListener("resize", this.updateDimensions.bind(this));
  }

  render() {
    let currentUser = JSON.parse(localStorage.getItem('currentUser'));
    const sidebarBackground = {
      backgroundImage: "url(" + this.props.image + ")"
    };
    return (


      <div
        id="sidebar"
        className="sidebar"
        data-color={this.props.color}
        data-image={this.props.image}
      >
        {this.props.hasImage ? (
          <div className="sidebar-background" />
        ) : (
            null
          )}
        <div className="logo">
          {/* <a
            href="https://www.creative-tim.com?ref=lbd-sidebar"
            className="simple-text logo-mini"
          > */}
          {/* <div className="logo-img">

            </div> */}
          {/* </a> */}
          <span className="logo-text simple-text logo-normal">
            Hedging Interest Differentials

          </span>
          {/* <a
            href="https://www.creative-tim.com?ref=lbd-sidebar"
            className="simple-text logo-normal"
          >
            Dashboard
          </a> */}
        </div>
        <div className="sidebar-wrapper">

          <ul className="nav">
            {this.state.width <= 991 ? <AdminNavbarLinks {...this.props} /> : null}
            {this.props.routes.map((prop, key) => {
              if (!prop.redirect)
                // console.log(prop.resources, 'resouces');
                return (
                  <li
                    className={
                      prop.upgrade
                        ? "active active-pro"
                        : this.activeRoute(prop.layout + prop.path)
                    }

                    key={key}
                  >
                    <NavLink
                      to={prop.layout + prop.path}
                      className="nav-link"
                      activeClassName="active"
                    >
                      <i className={prop.icon} />
                      <p
                        onClick={() => this.toggle(prop.id)}

                      >{prop.name}
                        {prop.child !== false && <b style={{ transform: prop.menu ? 'rotate(360deg)' : 'rotate(270deg)' }} className="caret"></b>}
                      </p>

                    </NavLink>
                    {prop.menu &&
                      <ul className='dropdown-menu-cutomize'>
                        {prop.resources != undefined && prop.resources.map((sub, index) => {

                          if (currentUser[0].role === "user" && sub.name !== "Users") {
                            return <NavLink
                              to={sub.layout + sub.path}
                              className="nav-link"
                              activeClassName="active"
                            >
                              <li

                                key={index}>
                                {/* <i className={sub.icon} /> */}
                                <p >{sub.name}</p>
                              </li>
                            </NavLink>
                          }
                          else if (currentUser[0].role === "admin") {
                            return <NavLink
                              to={sub.layout + sub.path}
                              className="nav-link"
                              activeClassName="active"
                              key={index}
                            >
                              <li

                               >
                                {/* <i className={sub.icon} /> */}
                                <p >{sub.name}</p>
                              </li>
                            </NavLink>
                          }



                        })}

                      </ul>
                    }

                  </li>
                );
              // return null;
            })}
          </ul>
        </div>
        {/* <NavLink>
          <DropdownButton id="dropdown-item-button" title="Dropdown button">
            <Dropdown.ItemText>Dropdown item text</Dropdown.ItemText>
            <Dropdown.Item as="button">Action</Dropdown.Item>
            <Dropdown.Item as="button">Another action</Dropdown.Item>
            <Dropdown.Item as="button">Something else</Dropdown.Item>
          </DropdownButton>
        </NavLink> */}

      </div>
    );
  }
}

export default Sidebar;

