import config from './constant/config';

const axios = require('axios');


const myInitObject = {}
export default myInitObject;


export const api = () => {
    let token = JSON.parse(localStorage.getItem("accessToken"));
    // Object.keys(myInitObject).forEach(key=> myInitObject[key]=null)
    // console.log(token, '-----------------+++++');
    axios.get(config.baseurl + '/accounts/fetchAllHistoryTrade', { headers: { 'x-access-token': token } })
        .then(function (response) {
            if (response.status == 200) {
                myInitObject.fetchAllHistoryTrade = response.data
                myInitObject.responsHT = true
            }
        })

    axios.get(config.baseurl + '/accounts/fetchAllOpenTrade', { headers: { 'x-access-token': token } })
        .then(function (response) {
            if (response.status == 200) {
                myInitObject.fetchAllOpenTrade = response.data
                myInitObject.responsOT = true
            }
        })

    axios.get(config.baseurl + '/accounts/fetchAllAccounts')
        .then(function (response) {
            if (response.status == 200) {
                myInitObject.fetchAllAccounts = response.data
                myInitObject.responsAC = true
            }
        });

    axios.get(config.baseurl + '/filter/fetchActivefilterdata', { headers: { 'x-access-token': token } })
        .then(function (response) {
            if (response.status == 200) {
                myInitObject.fetchActivefilterdata = response.data
                myInitObject.responsAF = true
            }
        });

    axios.get(config.baseurl + '/accounts/fetchLastUpdatedTime', { headers: { 'x-access-token': token } })
        .then(function (response) {
            if (response.status == 200) {
                myInitObject.fetchLastUpdatedTime = response.data
                myInitObject.responsLUT = true
            }
        });

    axios.get(config.baseurl + '/accounts/fetchStatusData', { headers: { 'x-access-token': token } })
        .then(function (response) {
            if (response.status == 200) {
                myInitObject.fetchStatusData = response.data
                myInitObject.responsSD = true
            }
        });

    axios.get(config.baseurl + '/profilepositions/calculatingOpenTrade', { headers: { 'x-access-token': token } })
        .then(function (response) {
            if (response.status == 200) {
                myInitObject.calculatingOpenTrade = response.data
                myInitObject.responsCOT = true

                axios.get(config.baseurl + '/profilepositions/calculatingHistoryTrade', { headers: { 'x-access-token': token } })
                    .then(function (response) {
                        if (response.status == 200) {
                            myInitObject.calculatingHistoryTrade = response.data
                            myInitObject.responsCHT = true

                            axios.get(config.baseurl + '/profilepositions/calculatingCommission', { headers: { 'x-access-token': token } })
                                .then(function (response) {
                                    if (response.status == 200) {
                                        myInitObject.calculatingCommission = response.data
                                        myInitObject.responsCC = true
                                    }
                                })
                        }
                    })
            }
        })
    axios.get(config.baseurl + '/accounts/fetchAllSymbol')
        .then(function (response) {
            if (response.status == 200) {
                myInitObject.fetchAllSymbol = response.data
                myInitObject.responsAS = true
            }
        })
    axios.get(config.baseurl + '/filter/fetchdailyswaps')
    .then(function (response) {
        if (response.status == 200) {
            myInitObject.fetchdailyswaps = response.data
            myInitObject.responsDS = true
        }
    })
    axios.get(config.baseurl + '/users/allAccounts?page=1')
        .then(function (response) {
            if (response.status == 200) {
                myInitObject.usersAllAccounts = response.data
                myInitObject.responsAAC = true
            }
        })

    axios.get(config.baseurl + '/filter/fetchfilterdata?page=1', { headers: { 'x-access-token': token } })
        .then(function (response) {
            if (response.status == 200) {
                myInitObject.fetchfilterdata = response.data
                myInitObject.responsFF = true
            }
        })
}
