import React, { Component } from 'react';
import 'react-calendar/dist/Calendar.css';
import "react-datepicker/dist/react-datepicker.css";
import { Grid, Row, Col, Button } from "react-bootstrap";
import TagsInput from 'react-tagsinput'
import { NotificationContainer, NotificationManager } from 'react-notifications';

import DatePicker from "react-datepicker";
import config from '../../constant/config.js';

import Select from 'react-select'
import { createLogicalAnd } from 'typescript';
const axios = require('axios');

class FilterEdite extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedAccountOption: null,
            selectedSymbolOption: null,
            selectedSymbolOptionTo: null,
            selectedAccountOptionTo: null,
            selectedCommissionAccount: null,
            startDate: null,
            endDate: null,
            startDateTo: null,
            endDateTo: null,
            allAccounts: [],
            allSymbols: [],
            profileName: '',
            showCommission: false,
            // showAddCommission: false,
            id: '',
            error: '',
            autoSyncClose: false,
            magicNumber: [],
            magicNumberTo: [],
            tags: [],
            include_exclude_status: '',
            showMagicNumber: false,
            showMagicNumberTo: false,
            exclude: false,
            include: false,
            excludeTo: false,
            includeTo: false,
            ticket:[],
            ticketTo:[],
            // showTicket: false,
            // showTicketTo: false,
            excludeTicket: false,
            includeTicket: false,
            excludeTicketTo: false,
            includeTicketTo: false,

        };


    }
    componentWillMount() {

        let self = this;
        axios.get(config.baseurl + '/accounts/fetchAllAccounts')
            .then(function (response) {
                if (response.status == 200) {
                    self.setState({ allAccounts: response.data.rows })
                }
            })
        axios.get(config.baseurl + '/accounts/fetchAllSymbol')
            .then(function (response) {
                if (response.status == 200) {
                    // console.log( response,"response ---------------fetchAllSymbol>")
                    self.setState({ allSymbols: response.data.rows })
                }
            })
    }
    componentDidMount() {

        let abc = this.props.data;
        console.log(abc, '------------------>>>');
        if (abc) {
            let from_symbol = JSON.parse(abc.from_symbols);
            let to_symbol = JSON.parse(abc.to_symbols);

            let all_from_symbols = from_symbol.map(data => { return ({ value: data, label: data }) });
            let all_to_symbols = to_symbol.map(data => { return ({ value: data, label: data }) });

            from_symbol = from_symbol.join(', ');
            to_symbol = to_symbol.join(', ');

            this.setState({
                id: abc.id,
                profileName: abc.profile_name,
                selectedAccountOption: ({ value: abc.accountFromInfo[0].id, label: abc.accountFromInfo[0].login }),
                selectedAccountOptionTo: ({ value: abc.accountToInfo[0].id, label: abc.accountToInfo[0].login }),
                showCommission: abc.commission_acount_id !== 0 ? true : false,
                selectedCommissionAccount: abc.commission_acount_id !== 0 ? ({ value: abc.accountCommissionInfo[0].id, label: abc.accountCommissionInfo[0].login }) : null,
                showMagicNumber: abc.from_include_exclude_status !== 0 ? true : false,
                showMagicNumberTo: abc.to_include_exclude_status !== 0 ? true : false,
                magicNumber: abc.from_magic_number != "0" && abc.from_magic_number != "" ? JSON.parse(abc.from_magic_number) : [],
                magicNumberTo: abc.to_magic_number != "0" && abc.to_magic_number != "" ? JSON.parse(abc.to_magic_number) : [],
                include: abc.from_include_exclude_status === 1 ? true : false,
                exclude: abc.from_include_exclude_status === 2 ? true : false,
                includeTo: abc.to_include_exclude_status === 1 ? true : false,
                excludeTo: abc.to_include_exclude_status === 2 ? true : false,
                // showTicket: abc.from_include_exclude_status_ticket !== 0 ? true : false,
                // showTicketTo: abc.to_include_exclude_status_ticket !== 0 ? true : false,
                ticket: abc.from_ticket != "0" && abc.from_ticket != "" ? JSON.parse(abc.from_ticket) : [],
                ticketTo: abc.to_ticket != "0" && abc.to_ticket != "" ? JSON.parse(abc.to_ticket) : [],
                includeTicket: abc.from_include_exclude_status_ticket === 1 ? true : false,
                excludeTicket: abc.from_include_exclude_status_ticket === 2 ? true : false,
                includeTicketTo: abc.to_include_exclude_status_ticket === 1 ? true : false,
                excludeTicketTo: abc.to_include_exclude_status_ticket === 2 ? true : false,
                selectedSymbolOption: all_from_symbols,
                autoSyncClose: abc.auto_close == 1 ? true : false,
                selectedSymbolOptionTo: all_to_symbols,
                startDate: new Date(abc.startdateFrom),
                startDateTo: new Date(abc.startdateTo),
                endDate: (abc.enddateFrom != null) ? new Date(abc.enddateFrom) : '',
                endDateTo: (abc.enddateTo != null) ? new Date(abc.enddateTo) : '',
            })


        }



    }

    handleDropdownChange = (e) => {
        this.setState({ selectValue: e.target.value });
    }
    handleChangeDate = date => {
        this.setState({
            startDate: date
        });
    }
    handleEndDate = date => {

        if (date < this.state.startDate && date != null) {
            this.setState({ error: 'enddate', endDate: "" })
            return
        }
        this.setState({
            error: '',
            endDate: date,
        });
    }
    handleChangeDateTo = date => {
        this.setState({
            startDateTo: date
        });
    }
    handleEndDateTo = date => {
        if (date < this.state.startDateTo && date != null) {
            this.setState({ error: 'enddateto', endDateTo: "" })
            return
        }
        this.setState({
            error: '',
            endDateTo: date,
        });
    }
    handleChange = (event) => {
        this.setState({ value: Array.from(event.target.selectedOptions, (item) => item.value) });
    }

    handeChangeShowCommission = () => {
        this.setState({ showCommission: !this.state.showCommission })
    }
    handleChangeCommissionAccount = selectedCommissionAccount => {
        this.setState({ selectedCommissionAccount });
    };

    renderCommissionAccount = () => {
        const { selectedCommissionAccount, allAccounts } = this.state;

        let options = allAccounts.map(data => {
            return { value: data.id, label: data.login };
        })
        return (
            <Select
                placeholder="Select Account"
                value={selectedCommissionAccount}
                onChange={this.handleChangeCommissionAccount}
                options={options}
            />
        )
    }



    renderAccountSelect = () => {
        const { selectedAccountOption, allAccounts } = this.state;
        let options = allAccounts.map(data => {
            return { value: data.id, label: data.login };
        })
        return (
            <Select
                placeholder="Select Account"
                value={selectedAccountOption}
                onChange={this.handleChangeAccount} l
                options={options}
                rules={{ required: 'Please select an option' }}
            />
        )
    }

    handleChangeAccountTo = selectedAccountOptionTo => {
        this.setState({ selectedAccountOptionTo, error: '' });
    };
    renderAccountSelectTo = () => {
        const { selectedAccountOptionTo, allAccounts } = this.state;

        let options = allAccounts.map(data => {
            return { value: data.id, label: data.login };
        })
        return (
            <Select
                placeholder="Select Account"
                value={selectedAccountOptionTo}
                onChange={this.handleChangeAccountTo}
                options={options}
            />
        )
    }

    handleChangeAccount = selectedAccountOption => {
        this.setState({ selectedAccountOption });
    };
    handleChangeSymbol = selectedSymbolOption => {
        this.setState({ selectedSymbolOption });
    };
    renderSymbolSelect = () => {
        const { selectedSymbolOption, allSymbols } = this.state;
        let options = allSymbols.map(data => {
            return { value: data.name, label: data.name };
        })
        return (
            <Select
                placeholder="Select Symbol"
                value={selectedSymbolOption}
                isMulti
                onChange={this.handleChangeSymbol}
                options={options}
            />
        )
    }
    handleChangeSymbolTo = selectedSymbolOptionTo => {
        this.setState({ selectedSymbolOptionTo });
    };
    renderSymbolSelectTo = () => {
        const { selectedSymbolOptionTo, allSymbols } = this.state;
        let options = allSymbols.map(data => {
            return { value: data.name, label: data.name };
        })
        return (
            <Select
                placeholder="Select Symbol"
                value={selectedSymbolOptionTo}
                isMulti
                onChange={this.handleChangeSymbolTo}
                options={options}
            />
        )
    }

    handleChangeMagicNumber = (magicNumber) => {


        // if(re.test(tags))
        this.setState({ magicNumber })
    }
    handleChangeMagicNumberTo = (magicNumberTo) => {


        // if(re.test(tags))
        this.setState({ magicNumberTo })
    }

    handleChangeTicket = (ticket) => {


        this.setState({ ticket })

    }
    handleChangeTicketTo = (ticketTo) => {

        this.setState({ ticketTo })
    }
    changeIncludeDefaultProperty = () => {

        this.setState({ include: !this.state.include, exclude: false })
            
        // if (!this.state.include == true) {
        //     this.setState({ showMagicNumber: true })
        // } else if (this.state.include == true) {
        //     this.setState({ showMagicNumber: false })

        // }


    }
    changeExcludeDefaultProperty = () => {
        this.setState({ exclude: !this.state.exclude, include: false })
        // if (!this.state.exclude == true) {
        //     this.setState({ showMagicNumber: true })
        // } else if (this.state.exclude == true) {
        //     this.setState({ showMagicNumber: false })

        // }

    }

    changeIncludeDefaultPropertyTo = () => {
        this.setState({ includeTo: !this.state.includeTo, excludeTo: false })
        // if (!this.state.includeTo == true) {
        //     this.setState({ showMagicNumberTo: true })
        // } else if (this.state.includeTo == true) {
        //     this.setState({ showMagicNumberTo: false })

        // }
    }
    changeExcludeDefaultPropertyTo = () => {
        this.setState({ excludeTo: !this.state.excludeTo, includeTo: false })
        // if (!this.state.excludeTo == true) {
        //     this.setState({ showMagicNumberTo: true })
        // } else if (this.state.excludeTo == true) {
        //     this.setState({ showMagicNumberTo: false })

        // }

    }

    changeIncludeDefaultPropertyTicket = () => {

        this.setState({ includeTicket: !this.state.includeTicketTo, excludeTicket: false }
        )
        // if (!this.state.includeTicket == true) {
        //     this.setState({ showTicket: true })
        // } else if (this.state.includeTicket == true) {
        //     this.setState({ showTicket: false })

        // }


    }
    changeExcludeDefaultPropertyTicket = () => {
        this.setState({ excludeTicket: !this.state.excludeTicket, includeTicket: false })
        // if (!this.state.excludeTicket == true) {
        //     this.setState({ showTicket: true })
        // } else if (this.state.excludeTicket == true) {
        //     this.setState({ showTicket: false })

        // }

    }

    changeIncludeDefaultPropertyTicketTo = () => {
        this.setState({ includeTicketTo: !this.state.includeTicketTo, excludeTicketTo: false })
        // if (!this.state.includeTicketTo == true) {
        //     this.setState({ showTicketTo: true })
        // } else if (this.state.includeTicketTo == true) {
        //     this.setState({ showTicketTo: false })

        // }
    }
    changeExcludeDefaultPropertyTicketTo = () => {
        this.setState({ excludeTicketTo: !this.state.excludeTicketTo, includeTicketTo: false })
        // if (!this.state.excludeTicketTo == true) {
        //     this.setState({ showTicketTo: true })
        // } else if (this.state.excludeTicketTo == true) {
        //     this.setState({ showTicketTo: false })

        // }

    }

    handleprofileName(e) {

        this.setState({ profileName: e.target.value.charAt(0).toUpperCase() + e.target.value.slice(1), error: '' })

    }
    handleChangeAutoSync(e) {
        this.setState({ autoSyncClose: !this.state.autoSyncClose }, () => {
        })
    }
    formDataSubmit(e) {
        e.preventDefault();

        let from_status = this.state.include === true && this.state.exclude === false ? 1 : this.state.exclude === true && this.state.include === false ? 2 : 0

        let to_status = this.state.includeTo === true && this.state.excludeTo === false ? 1 : this.state.excludeTo === true && this.state.includeTo === false ? 2 : 0

        let from_status_ticket = this.state.includeTicket === true && this.state.excludeTicket === false ? 1 : this.state.excludeTicket === true && this.state.includeTicket === false ? 2 : 0

        let to_status_ticket = this.state.includeTicketTo === true && this.state.excludeTicketTo === false ? 1 : this.state.excludeTicketTo === true && this.state.includeTicketTo === false ? 2 : 0
        
        let self = this

       
        if (this.state.profileName == null || this.state.profileName == '') {
            NotificationManager.listNotify = [];
            NotificationManager.error ('Please select profile name !')
            return
        }
        else if (this.state.selectedAccountOption == null || this.state.selectedAccountOption == '') {
            this.setState({ error: 'accountoption' })
            return
        }
        else if (this.state.showCommission == true  &&  (this.state.selectedCommissionAccount == null || this.state.selectedCommissionAccount == '')) {
            this.setState({ error: 'selectedCommissionAccount' })
            return
        }
        else if (this.state.startDate == null || this.state.startDate == '') {
            this.setState({ error: 'startdate' })
            return
        } else if (this.state.selectedSymbolOption == null || this.state.selectedSymbolOption == '') {
            this.setState({ error: 'symbolselect' })
            return
        }
        else if (this.state.selectedAccountOptionTo == null || this.state.selectedAccountOptionTo == '') {
            this.setState({ error: 'accountoptionto' })
            return
        } else if (this.state.startDateTo == null || this.state.startDateTo == '') {
            this.setState({ error: 'startDateTo' })
            return
        }
        else if (this.state.selectedSymbolOptionTo == null || this.state.selectedSymbolOptionTo == '') {
            this.setState({ error: 'selectedSymbolOptionTo' })
            return
        }
        else {
            this.setState({ error: '' })
        }
        let fromSymbol = this.state.selectedSymbolOption;
        let toSymbol = this.state.selectedSymbolOptionTo;

        let symbolFrom = fromSymbol.map((data) => data.value)
        let symbolto = toSymbol.map((data) => data.value)

        let Data = {
            id: this.state.id,
            profile_name: this.state.profileName,
            from_account_id: this.state.selectedAccountOption.value,
            to_account_id: this.state.selectedAccountOptionTo.value,
            commission_acount_id: this.state.showCommission == true ? this.state.selectedCommissionAccount.value : 0,
            startdateFrom: this.state.startDate,
            // enddateFrom: this.state.endDate,
            enddateFrom: (this.state.endDate) ? this.state.endDate : null,
            startdateTo: this.state.startDateTo,
            enddateTo: (this.state.endDateTo) ? this.state.endDateTo : null,
            from_symbols: JSON.stringify(symbolFrom),
            to_symbols: JSON.stringify(symbolto),
            auto_close: this.state.autoSyncClose ? 1 : 0,
            from_magic_number: (from_status != 0) ? this.state.magicNumber.length > 0 ? JSON.stringify(this.state.magicNumber) : JSON.stringify([]) : "",
            to_magic_number: (to_status != 0) ? this.state.magicNumberTo.length > 0 ? JSON.stringify(this.state.magicNumberTo) : JSON.stringify([]) : "",

            from_include_exclude_status: from_status,
            to_include_exclude_status: to_status,
            from_ticket:(from_status_ticket != 0) ? this.state.ticket.length > 0 ? JSON.stringify(this.state.ticket) : JSON.stringify([]) : "",
          to_ticket: (to_status_ticket != 0) ? this.state.ticketTo.length > 0 ? JSON.stringify(this.state.ticketTo) : JSON.stringify([]) : "",
          from_include_exclude_status_ticket: from_status_ticket,
                to_include_exclude_status_ticket: to_status_ticket, 
            createdAt: new Date(),
            updatedAt: new Date()
        }
        
        axios.post(config.baseurl + '/filter/updatefilterdatafull', Data)
        .then(function (response) {
            
            if (response.status == 200) {
                    self.props.onHide()

                }
            })
            .catch(function (error) {
            });

    }
    crossClick(e) {
        e.preventDefault();
        this.props.onHide()
    }

    render() {
        return (
            <div className="content">
                <Grid fluid>
                    <div className="symbol-box">
                        <div className="cross_div"><a onClick={(e) => this.crossClick(e)}><i className="fa fa-times" aria-hidden="true"></i></a></div>
                        <div className="terminal-text" >
                            <p className="profilename pnameadjust"> Profile Name</p>
                            <input type="text" name="profilename" value={this.state.profileName} className="Itext" onChange={(e) => this.handleprofileName(e)} />
                            {(this.state.error == 'profileName') && <p style={{ color: 'red', fontSize: '13px' }}>*Please Fill Your Profile Name </p>}

                        </div>
                        <div className="synclose">
                            <label class="checkbox-inline"><input type="checkbox" checked={this.state.autoSyncClose} name="changeAutoSync" onChange={(e) => this.handleChangeAutoSync(e)} /><b>Auto Sync Close</b></label>
                        </div>
                        <form>
                            <Row>
                                {/* <Col md={12}>
                                    <div className="card">
                                        <div style={{ padding: '10px 20px 35px ', }}>
                                            <div>
                                                <h3 className=" text-center">Filter By Magic Numbers</h3>

                                            </div>
                                            <div className="row">
                                                <div className="col-md-6 text-center">

                                                    <button type="button" class="text-center filter-add-button">Include</button>
                                                </div>

                                                <div className="col-md-6 text-center btnspace">
                                                    <button type="button" class="filter-add-button">Exclude</button>
                                                </div>
                                                <div className="row">
                                                    <div className="col">
                                                        <div class="text-center  ">
                                                            <input class="form-control input-css" placeholder="Select Numbers" type="number"></input>
                                                        </div>
                                                    </div>
                                                </div>from_include_exclude_status

                                            </div>

                                        </div>
                                    </div>
                                </Col> */}
                                <Col md={6}>
                                    <div className="card bottom">
                                        <div style={{ padding: '10px 20px 0px' }}>
                                            <span style={{ fontWeight: 'bold' }}>Account A</span>
                                        </div>
                                        <table className="table mt20">
                                            <tbody>
                                                <tr>
                                                    <td className="b-clr">
                                                        <span className="accnt">Account :</span>
                                                    </td>

                                                    <td className="b-clr text-center" style={{ width: '40%' }}>
                                                        {this.renderAccountSelect()}
                                                        {(this.state.error == 'accountoption') && <p style={{ color: 'red', fontSize: '13px' }}>*Please Select Your Account</p>}

                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr ">
                                                        <span className="accnt">From:  </span>
                                                    </td>
                                                    <td className="b-clr text-center datepicker_filter">
                                                        <DatePicker
                                                            selected={this.state.startDate}
                                                            onChange={this.handleChangeDate}
                                                            isClearable
                                                            placeholderText="dd/mm/yyyy"
                                                            dateFormat="dd/MM/yyyy"
                                                            startDate={this.state.startDate}
                                                            endDate={this.state.endDate}
                                                            strictParsing
                                                        />
                                                        {(this.state.error == 'startdate') && <p style={{ color: 'red', fontSize: '13px' }}>*Please Select Start Date</p>}
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr ">
                                                        <span className="accnt">To :  </span>
                                                    </td>
                                                    <td className="b-clr text-center datepicker_filter">
                                                        <DatePicker

                                                            selected={this.state.endDate}
                                                            onChange={this.handleEndDate}
                                                            isClearable
                                                            placeholderText="dd/mm/yyyy"
                                                            dateFormat="dd/MM/yyyy"
                                                            startDate={this.state.startDate}
                                                            endDate={this.state.endDate}
                                                        />
                                                        {(this.state.error == 'enddate') && <p style={{ color: 'red', fontSize: '13px' }}>*
                                                        Please ensure that the End Date is greater than  the Start Date.</p>}
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr ">
                                                        <span className="accnt">Symbol :  </span>
                                                    </td>
                                                    <td className="b-clr text-center" style={{ width: '50%' }}>
                                                        {this.renderSymbolSelect()}
                                                        {(this.state.error == 'symbolselect') && <p style={{ color: 'red', fontSize: '13px' }}>*Please Select Your Symbol </p>}

                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr ">
                                                            <span className="accnt">Magic Number </span>
                                                        </td>
                                                        <td className="b-clr text-center" style={{ width: '40%' }}>
                                                        <TagsInput
                                                        onValidationReject={() => console.log('rejected')}
                                                        validationRegex={/^[0-9]*$/}
                                                        inputProps={{ placeholder: this.state.magicNumber.length > 0 ? "" : " Enter Magic Number" }} type="number" value={this.state.magicNumber
                                                        } onChange={this.handleChangeMagicNumber}></TagsInput>
                                                            </td>
                                                    </tr>
                                                    <tr>
                                                        <td className="b-clr dataAdjust">
                                                            <div onClick={this.changeIncludeDefaultProperty}>
                                                                <input
                                                                    type="radio" id="include" name="include" checked={this.state.include} />
                                                                <span className="accnt ">Include</span>

                                                            </div>
                                                        </td>
                                                        <td className="b-clr text-center">
                                                            <div onClick={this.changeExcludeDefaultProperty} >
                                                                <input type="radio" id="exclude" name="exclude" checked={this.state.exclude} />
                                                                <span className="accnt ">Exclude</span>
                                                            </div>
                                                        </td>
                                                    </tr>
                                                    <tr>
                                                    <td className="b-clr ">
                                                            <span className="accnt">Ticket </span>
                                                        </td>
                                                        <td className="b-clr text-center" style={{ width: '40%' }}>
                                                        <TagsInput
                                                        onValidationReject={() => console.log('rejected')}
                                                        validationRegex={/^[0-9]*$/}
                                                        inputProps={{ placeholder: this.state.ticket.length > 0 ? "" : " Enter Ticket" }} type="number" value={this.state.ticket
                                                        } onChange={this.handleChangeTicket}></TagsInput>
                                                            </td>
                                                    </tr>
                                                    <tr>
                                                        <td className="b-clr dataAdjust">
                                                            <div onClick={this.changeIncludeDefaultPropertyTicket}>
                                                                <input
                                                                    type="radio" id="includeTicket" name="includeTicket" checked={this.state.includeTicket} />
                                                                <span className="accnt ">Include</span>

                                                            </div>
                                                        </td>
                                                        <td className="b-clr text-center">
                                                            <div onClick={this.changeExcludeDefaultPropertyTicket} >
                                                                <input type="radio" id="excludeTicket" name="excludeTicket" checked={this.state.excludeTicket} />
                                                                <span className="accnt ">Exclude</span>
                                                            </div>
                                                        </td>
                                                    </tr>

                                            </tbody>
                                        </table>
                                        {/* <div className="text-center">
                                            <span c> Magic Number :  </span>
                                        </div>

                                        <table className="table ">
                                            <tbody>
                                                <tr>
                                                    <td className="b-clr dataAdjust">
                                                        <div onClick={this.changeIncludeDefaultProperty}>
                                                            <input
                                                                type="radio" id="include" name="include" checked={this.state.include} />
                                                            <span class="accnt ">Include</span>

                                                        </div>
                                                    </td>
                                                    <td className="b-clr ">
                                                        <div onClick={this.changeExcludeDefaultProperty} >
                                                            <input type="radio" id="exclude" name="exclude" checked={this.state.exclude} />
                                                            <span class="accnt ">Exclude</span>
                                                        </div>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>


                                        {this.state.showMagicNumber &&
                                            <div className="b-clr magicnumber" style={{}}>
                                                <TagsInput
                                                    onValidationReject={() => console.log('rejected')}
                                                    validationRegex={/^[0-9]*$/}
                                                    inputProps={{ placeholder: this.state.magicNumber.length > 0 ? "" : " Enter Magic Number" }} type="number" value={this.state.magicNumber
                                                    } onChange={this.handleChangeMagicNumber}></TagsInput>
                                            </div>}

                                            <div className="text-center">
                                                <span > Ticket   </span>
                                            </div>

                                            <table className="table ">
                                                <tbody>
                                                    <tr>
                                                        <td className="b-clr dataAdjust">
                                                            <div onClick={this.changeIncludeDefaultPropertyTicket}>
                                                                <input
                                                                    type="radio" id="includeTicket" name="includeTicket" checked={this.state.includeTicket} />
                                                                <span className="accnt ">Include</span>

                                                            </div>
                                                        </td>
                                                        <td className="b-clr text-center">
                                                            <div onClick={this.changeExcludeDefaultPropertyTicket} >
                                                                <input type="radio" id="excludeTicket" name="excludeTicket" checked={this.state.excludeTicket} />
                                                                <span className="accnt ">Exclude</span>
                                                            </div>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>


                                            {this.state.showTicket === true ?
                                                <div className="b-clr magicnumber" style={{}}>
                                                    <TagsInput
                                                        onValidationReject={() => console.log('rejected')}
                                                        validationRegex={/^[0-9]*$/}
                                                        inputProps={{ placeholder: this.state.ticket.length > 0 ? "" : " Enter Ticket" }} type="number" value={this.state.ticket
                                                        } onChange={this.handleChangeTicket}></TagsInput>
                                                </div> : null} */}
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="card bottom">
                                        <div style={{ padding: '10px 20px 0px' }}>
                                            <span style={{ fontWeight: 'bold' }}>Account B</span>
                                        </div>

                                        <table className="table mt20">
                                            <tbody>
                                                <tr>
                                                    <td className="b-clr ">
                                                        <span className="accnt">Account :</span>
                                                    </td>
                                                    <td className="b-clr text-center" style={{ width: '50%' }}>
                                                        {this.renderAccountSelectTo()}
                                                        {(this.state.error == 'accountoptionto') && <p style={{ color: 'red', fontSize: '13px' }}>*Please Select Your Account</p>}


                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr ">
                                                        <span className="accnt">From:  </span>
                                                    </td>
                                                    <td className="b-clr text-center datepicker_filter">
                                                        <DatePicker
                                                            selected={this.state.startDateTo}
                                                            onChange={this.handleChangeDateTo}
                                                            isClearable
                                                            placeholderText="dd/mm/yyyy"
                                                            dateFormat="dd/MM/yyyy"
                                                            startDate={this.state.startDateTo}
                                                            endDate={this.state.endDateTo}
                                                        />

                                                        {(this.state.error == 'startDateTo') && <p style={{ color: 'red', fontSize: '13px' }}>*Please Select Start Date</p>}

                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr ">
                                                        <span className="accnt">To :  </span>
                                                    </td>
                                                    <td className="b-clr text-center datepicker_filter">
                                                        <DatePicker
                                                            selected={this.state.endDateTo}
                                                            onChange={this.handleEndDateTo}
                                                            isClearable
                                                            placeholderText="dd/mm/yyyy"
                                                            dateFormat="dd/MM/yyyy"
                                                            startDate={this.state.startDateTo}
                                                            endDate={this.state.endDateTo}

                                                        />

                                                        {(this.state.error == 'enddateto') && <p style={{ color: 'red', fontSize: '13px' }}>*
                                                        Please ensure that the End Date is greater than  the Start Date.</p>}

                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr">
                                                        <span className="accnt">Symbol :  </span>
                                                    </td>
                                                    <td className="b-clr text-center" style={{ width: '40%' }}>
                                                        {this.renderSymbolSelectTo()}
                                                        {(this.state.error == 'selectedSymbolOptionTo') && <p style={{ color: 'red', fontSize: '13px' }}>*Please Select Your Symbol </p>}


                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td className="b-clr ">
                                                            <span className="accnt">Magic Number </span>
                                                        </td>
                                                        <td className="b-clr text-center" style={{ width: '40%' }}>
                                                        <TagsInput
                                                            onValidationReject={() => console.log('rejected')}
                                                            validationRegex={/^[0-9]*$/}
                                                            inputProps={{ placeholder: this.state.magicNumberTo.length > 0 ? "" : " Enter Magic Number" }} type="number" value={this.state.magicNumberTo
                                                            } onChange={this.handleChangeMagicNumberTo}></TagsInput>
                                                            </td>
                                                    </tr>
                                                    <tr>
                                                        <td className="b-clr checkboxAdjust">
                                                            <div onClick={this.changeIncludeDefaultPropertyTo} >
                                                                <input
                                                                    type="radio" id="includeTo" name="includeTo" checked={this.state.includeTo} />
                                                                <span className="accnt ">Include</span>

                                                            </div>
                                                        </td>
                                                        <td className="b-clr checkboxAdjust text-center">
                                                            <div onClick={this.changeExcludeDefaultPropertyTo} >
                                                                <input type="radio" id="excludeTo" name="excludeTo" checked={this.state.excludeTo} />
                                                                <span className="accnt ">Exclude</span>
                                                            </div>
                                                        </td>
                                                    </tr>
                                                    <tr>
                                                    <td className="b-clr ">
                                                            <span className="accnt">Ticket </span>
                                                        </td>
                                                        <td className="b-clr text-center" style={{ width: '40%' }}>
                                                        <TagsInput
                                                            onValidationReject={() => console.log('rejected')}
                                                            validationRegex={/^[0-9]*$/}
                                                            inputProps={{ placeholder: this.state.ticketTo.length > 0 ? "" : " Enter Ticket" }} type="number" value={this.state.ticketTo
                                                            } onChange={this.handleChangeTicketTo}></TagsInput>
                                                            </td>
                                                    </tr>
                                                    <tr>
                                                        <td className="b-clr checkboxAdjust">
                                                            <div onClick={this.changeIncludeDefaultPropertyTicketTo} >
                                                                <input
                                                                    type="radio" id="includeTicketTo" name="includeTicketTo" checked={this.state.includeTicketTo} />
                                                                <span className="accnt ">Include</span>

                                                            </div>
                                                        </td>
                                                        <td className="b-clr checkboxAdjust text-center">
                                                            <div onClick={this.changeExcludeDefaultPropertyTicketTo} >
                                                                <input type="radio" id="excludeTicketTo" name="excludeTicketTo" checked={this.state.excludeTicketTo} />
                                                                <span className="accnt ">Exclude</span>
                                                            </div>
                                                        </td>
                                                    </tr>
                                            </tbody>
                                        </table>


                                        {/* <div className="text-center">
                                            <span > Magic Number :  </span>
                                        </div>
                                        <table className="table mt20">
                                            <tbody>

                                                <tr>
                                                    <td className="b-clr checkboxAdjust">
                                                        <div onClick={this.changeIncludeDefaultPropertyTo} >
                                                            <input
                                                                type="radio" id="includeTo" name="includeTo" checked={this.state.includeTo} />
                                                            <span class="accnt ">Include</span>

                                                        </div>
                                                    </td>
                                                    <td className="b-clr checkboxAdjust">
                                                        <div onClick={this.changeExcludeDefaultPropertyTo} >
                                                            <input type="radio" id="excludeTo" name="excludeTo" checked={this.state.excludeTo} />
                                                            <span class="accnt ">Exclude</span>
                                                        </div>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                        <div>

                                            {this.state.showMagicNumberTo === true ?
                                                <div className="b-clr magicnumber" style={{}}>
                                                    <TagsInput
                                                        onValidationReject={() => console.log('rejected')}
                                                        validationRegex={/^[0-9]*$/}
                                                        inputProps={{ placeholder: this.state.magicNumber.length > 0 ? "" : " Enter Magic Number" }} type="number" value={this.state.magicNumberTo
                                                        } onChange={this.handleChangeMagicNumberTo}></TagsInput>
                                                </div> : null}
                                        </div>

                                        <div className="text-center">
                                                <span > Ticket  </span>
                                            </div>
                                            <table className="table mt20">
                                                <tbody>

                                                    <tr>
                                                        <td className="b-clr checkboxAdjust">
                                                            <div onClick={this.changeIncludeDefaultPropertyTicketTo} >
                                                                <input
                                                                    type="radio" id="includeTicketTo" name="includeTicketTo" checked={this.state.includeTicketTo} />
                                                                <span className="accnt ">Include</span>

                                                            </div>
                                                        </td>
                                                        <td className="b-clr checkboxAdjust text-center">
                                                            <div onClick={this.changeExcludeDefaultPropertyTicketTo} >
                                                                <input type="radio" id="excludeTicketTo" name="excludeTicketTo" checked={this.state.excludeTicketTo} />
                                                                <span className="accnt ">Exclude</span>
                                                            </div>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                            <div>

                                                {this.state.showTicketTo === true ?
                                                    <div className="b-clr magicnumber" style={{}}>
                                                        <TagsInput
                                                            onValidationReject={() => console.log('rejected')}
                                                            validationRegex={/^[0-9]*$/}
                                                            inputProps={{ placeholder: this.state.ticketTo.length > 0 ? "" : " Enter Ticket" }} type="number" value={this.state.ticketTo
                                                            } onChange={this.handleChangeTicketTo}></TagsInput>
                                                    </div> : null}
                                            </div> */}
                                    </div>
                                </Col>
                            </Row>
                            <hr></hr>
                            <div className="chceckbox">

                                <label class="checkbox-inline"><input type="checkbox" checked={this.state.showCommission} onChange={this.handeChangeShowCommission} /><b>Add Commission</b></label>
                            </div>



                            {this.state.showCommission &&
                                <div className="center-com d-flex">
                                    <div className="com">
                                        <h3 style={{ fontWeight: 'bold' }}>Commission</h3>

                                    </div>
                                    <table className="table mt20">

                                        <tbody>
                                            <tr>

                                                <td className="b-clr ">
                                                    <span className="accnt"> Account :</span>
                                                </td>
                                                <td className="b-clr text-center" style={{ width: '50%' }}>
                                                    {this.renderCommissionAccount()}
                                                    {(this.state.error == 'selectedCommissionAccount') && <p style={{ color: 'red', fontSize: '13px' }}>*Please Select Your Account </p>}
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>

                            }
                            <div className="save_button_filter">
                                <Button className="btn-primary savebutton" variant="primary" size="lg" block onClick={(e) => this.formDataSubmit(e)}>
                                    <b>Update</b>
                                </Button>
                            </div>
                        </form>
                    </div>
                </Grid>
            </div>
        );
    }
}

export default FilterEdite;
