
import React from "react";
import ReactDOM from "react-dom";

import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";

import "bootstrap/dist/css/bootstrap.min.css";
import "./assets/css/animate.min.css";
import "./assets/sass/light-bootstrap-dashboard-react.scss?v=1.3.0";
import "./assets/css/demo.css";
import "./assets/css/pe-icon-7-stroke.css";
import "./assets/css/dashboard.css";
import innerfilter from "./views/filter/innerfilter";
import Login from "./views/login";


import AdminLayout from "./layouts/Admin.jsx";

ReactDOM.render(
  <BrowserRouter>
    <Switch>
      <Route path="/dashboard" render={props => <AdminLayout {...props} />} />

      <Route exact path="/dashboard/innerfilter" component={innerfilter} />
      <Route exact from="/login" component={Login} />
      {/* <Redirect from="/" to="/admin/dashboard" /> */}
      {/* <Redirect from="/" component={Login} /> */}
      <Redirect from="/" to="/login"/>
      {/* <Route exact path="/innerfilter" component={innerfilter} /> */}

    </Switch>
  </BrowserRouter>,
  document.getElementById("root")
);
