import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
import NotificationSystem from "react-notification-system";
import IdleTimer from 'react-idle-timer';
import AdminNavbar from "components/Navbars/AdminNavbar";
// import Footer from "components/Footer/Footer";
import Sidebar from "components/Sidebar/Sidebar";
// import FixedPlugin from "components/FixedPlugin/FixedPlugin.jsx";

import { style } from "variables/Variables.jsx";

import routes from "routes.js";

import image from "assets/img/sidebar-3.jpg";
import { api } from '../api'

class Admin extends Component {
  _isMounted = false;
  constructor(props) {
    super(props);
    this.state = {
      _notificationSystem: null,
      image: image,
      color: "black",
      hasImage: true,
      fixedClasses: "dropdown show-dropdown open",
      timeout: 1000 * 900 * 1,
      userLoggedIn: false,
      isTimedOut: false
    };
    this.idleTimer = null
    this.onAction = this._onAction.bind(this)
    this.onActive = this._onActive.bind(this)
    this.onIdle = this._onIdle.bind(this)
    this.handleLogout = this.handleLogout.bind(this)
  }


  handleLogout() {
    // console.log(new Date());
    localStorage.clear()
    this.props.history.push('/login')
  }
  _onAction(e) {
    // console.log('user did something', e)
    this.setState({ isTimedOut: false })
    // console.log(new Date());
  }

  _onActive(e) {
    // console.log('user is active', e)
    this.setState({ isTimedOut: false })
  }

  _onIdle(e) {
    // console.log('user is idle', e)

    const isTimedOut = this.state.isTimedOut
    if (isTimedOut) {
      this.handleLogout()
    } else {
      this.idleTimer.reset();
      this.setState({ isTimedOut: true })
    }

  }
  // handleNotificationClick = position => {
  //   var color = Math.floor(Math.random() * 4 + 1);
  //   var level;
  //   switch (color) {
  //     case 1:
  //       level = "success";
  //       break;
  //     case 2:
  //       level = "warning";
  //       break;
  //     case 3:
  //       level = "error";
  //       break;
  //     case 4:
  //       level = "info";
  //       break;
  //     default:
  //       break;
  //   }
  //   this.state._notificationSystem.addNotification({
  //     title: <span data-notify="icon" className="pe-7s-gift" />,
  //     message: (
  //       <div>
  //         Welcome to <b>Light Bootstrap Dashboard</b> - a beautiful freebie for
  //         every web developer.
  //       </div>
  //     ),
  //     level: level,
  //     position: position,
  //     autoDismiss: 15
  //   });
  // };
  getRoutes = routes => {
    let routeArray = []
    routes.map((data) => {
      routeArray.push(data)
      let role = JSON.parse(localStorage.getItem('currentUser'))[0].role;
      if (role === "user" && data.child === true) {
        data.resources.map((prop) => {
          if (prop.name !== "Users") {
            routeArray.push(prop)
          }
        })
      } else if (data.child === true) {
        data.resources.map((prop) => {
          routeArray.push(prop)
        })
      }
    })
    return routeArray.map((prop, key) => {
      if (prop.layout === "/dashboard") {
        return (
          <Route
            path={prop.layout + prop.path}
            render={props => (
              <prop.component
                {...props}
                handleClick={this.handleNotificationClick}
              />
            )}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };
  getBrandText = path => {
    for (let i = 0; i < routes.length; i++) {
      if (
        this.props.location.pathname.indexOf(
          routes[i].layout + routes[i].path
        ) !== -1
      ) {
        return routes[i].name;
      }
    }
    // return "Brand";
  };
  handleImageClick = image => {
    this.setState({ image: image });
  };
  handleColorClick = color => {
    this.setState({ color: color });
  };
  handleHasImage = hasImage => {
    this.setState({ hasImage: hasImage });
  };
  handleFixedClick = () => {
    if (this.state.fixedClasses === "dropdown") {
      this.setState({ fixedClasses: "dropdown show-dropdown open" });
    } else {
      this.setState({ fixedClasses: "dropdown" });
    }
  };
  componentDidMount() {
    this._isMounted = true;
    if (this._isMounted) {
      let currentUser = JSON.parse(localStorage.getItem('currentUser'));
      if (currentUser === null) {
        this.props.history.push('/login')
        return;
      } else {
        api()
        setInterval(() => {
          api()
        }, 300000);

      }
    }

    //   this.setState({ _notificationSystem: this.refs.notificationSystem });
    //   var _notificationSystem = this.refs.notificationSystem;
    //   var color = Math.floor(Math.random() * 4 + 1);
    //   var level;
    //   switch (color) {
    //     case 1:
    //       level = "success";
    //       break;
    //     case 2:
    //       level = "warning";
    //       break;
    //     case 3:
    //       level = "error";
    //       break;
    //     case 4:
    //       level = "info";
    //       break;
    //     default:
    //       break;
    //   }
    //   _notificationSystem.addNotification({
    //     title: <span data-notify="icon" className="pe-7s-gift" />,
    //     message: (
    //       <div>
    //         Welcome to <b>Light Bootstrap Dashboard</b> - a beautiful freebie for
    //         every web developer.
    //       </div>Modal
    //     ),
    //     level: level,
    //     position: "tr",
    //     autoDismiss: 15
    //   });
  }
  componentDidUpdate(e) {
    if (
      window.innerWidth < 993 &&
      e.history.location.pathname !== e.location.pathname &&
      document.documentElement.className.indexOf("nav-open") !== -1
    ) {
      document.documentElement.classList.toggle("nav-open");
    }
    if (e.history.action === "PUSH") {
      document.documentElement.scrollTop = 0;
      document.scrollingElement.scrollTop = 0;
      this.refs.mainPanel.scrollTop = 0;
    }
  }
  componentWillUnmount() {
    this._isMounted = false;
  }



  render() {
    return (
      <>
        <IdleTimer
          ref={ref => { this.idleTimer = ref }}
          element={document}
          onActive={this.onActive}
          onIdle={this.onIdle}
          onAction={this.onAction}
          debounce={250}
          timeout={this.state.timeout} />
        <div className="wrapper">
          <NotificationSystem ref="notificationSystem" style={style} />
          <Sidebar {...this.props} routes={routes} image={this.state.image}
            color={this.state.color}
            hasImage={this.state.hasImage} />
          <div id="main-panel" className="main-panel" ref="mainPanel">
            <AdminNavbar
              {...this.props}
              brandText={this.getBrandText(this.props.location.pathname)}
            />
            <Switch>{this.getRoutes(routes)}</Switch>
            {/* <Footer /> */}
            {/* <FixedPlugin
            handleImageClick={this.handleImageClick}
            handleColorClick={this.handleColorClick}
            handleHasImage={this.handleHasImage}
            bgColor={this.state["color"]}
            bgImage={this.state["image"]}
            mini={this.state["mini"]}
            handleFixedClick={this.handleFixedClick}
            fixedClasses={this.state.fixedClasses}
          /> */}
          </div>
        </div>
      </>
    );
  }
}

export default Admin;
