             allHistoryRecord: myInitObject.fetchAllHistoryTrade.rows,
             fromHistoryOrderInfo: myInitObject.fetchAllHistoryTrade.fromHistoryOrderInfo,
             toHistoryOrderInfo: myInitObject.fetchAllHistoryTrade.toHistoryOrderInfo,
             //open trade
             allOpenRecord: myInitObject.fetchAllOpenTrade.rows,
             fromOpenOrderInfo: myInitObject.fetchAllOpenTrade.fromOpenOrderInfo,
             toOpenOrderInfo: myInitObject.fetchAllOpenTrade.toOpenOrderInfo,
            
