import React, { Component } from 'react';
import { Grid, Row, Col, Modal, Alert } from "react-bootstrap";
import swal from 'sweetalert';
import config from '../../constant/config.js';
import { NotificationContainer, NotificationManager } from 'react-notifications';
const axios = require('axios');


class Login extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loginForm: false,
            setShow: false,
            show: true,
            username: '',
            password: '',
            alert: null,
            persons: [],
            shwPwd: false,
            shwMsg: false,
            invalidUsername: '',
            invalidPassword: '',
            accountlist: []

        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);

    }

    onChange(e) {
        this.setState({ [e.target.name]: e.target.value.trim() })
    }
    onSubmit(e) {
        e.preventDefault();
        const userdetail = {
            username: this.state.username,
            password: this.state.password
        }
        if (this.state.username == '') {
            this.setState({ shwPwd: true })
        }

        let self = this;
        axios.post(config.baseurl + '/users/mainLogin', userdetail)
            .then(function (response) {
                if (response.status == 200) {
                    // self.handleClose();
                    if (response.data.rows && response.data.rows.length > 0) {
                        localStorage.setItem('currentUser', JSON.stringify(response.data.rows));
                        localStorage.setItem('accessToken', JSON.stringify(response.data.accessToken));
                        self.props.history.push('/dashboard/dashboard')
                    }
                    if (response.data.message) {
                        if (response.data.name == 'username') {
                            self.setState({ invalidUsername: response.data.message })
                            if (self.state.invalidUsername == '' || self.state.invalidUsername == 'Invalid username.' || self.state.invalidUsername == "Invalid username") {
                                self.createNotification()
                            }
                        }
                        if (response.data.name == 'password') {
                            if (self.state.invalidPassword == '' || self.state.invalidPassword == 'Invalid password.') {
                                self.createNotification()
                            }
                            self.setState({ invalidPassword: response.data.message, invalidUsername: '' })

                        }
                    }
                }
            })
            .catch(function (err) {
            });
    }
    toogle = () => {
        this.setState({ loginForm: !this.state.loginForm })
    }
    handleClose = () => {
        this.setState({ loginForm: false })
    }
    handleShow = () => {
        this.setState({ setShow: true })
    }
    createNotification = () => {
        NotificationManager.listNotify = [];
        NotificationManager.error('Invalid Credentials !');
    }

    componentDidMount() {
        // let self = this;

        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if (currentUser !== null) {
            this.props.history.push('dashboard/dashboard')
            return;
        }
        // axios.get(config.baseurl + '/users/allAccounts')
        //     .then(function (response) {
        //         if (response.status == 200) {
        //             self.setState({ accountlist: response.data.rows })
        //         }
        //     })
    }
    render() {
        const { invalidPassword, invalidUsername } = this.state;
        return (
            <div className="content">
                <Grid fluid style={{ marginTop: '20vh' }}>
                    <Row>
                        <Col md={4} />
                        <Col
                            md={4}
                            sm={6}
                        >
                            <span className="login-form-header">Rimon Hedge Funds Monitoring</span>
                            <div className="form-box login-box">
                                {/* <Modal.Title className="text-center"><b>Log In</b></Modal.Title> */}

                                {/* <div className="container-fluid" style={{ background: '#eee', padding: 15, marginTop: 10 }}>
                                    <div>
                                        <span style={{ fontWeight: 'bold' }}> Username: </span>
                                                    testaccount
                                                </div>
                                    <div>
                                        <span style={{ fontWeight: 'bold' }}> Password: </span>
                                                    1234567
                                                </div>
                                </div> */}
                                <form style={{ marginTop: '20px' }} onSubmit={this.onSubmit}>
                                    <label>Username</label>

                                    <input className="Itext" placeholder="Enter Username" name="username" value={this.state.username} onChange={this.onChange}
                                    // onKeyUp={this.checkValidUser}
                                    ></input>
                                    <label>Password</label>

                                    <input className="Itext" placeholder="Enter Password" name="password" type="password" value={this.state.password} onChange={this.onChange}></input>

                                    <div className="clearfix">
                                        <button type="submit" disabled={!this.state.username || !this.state.password} className={!this.state.username || !this.state.password ? "loginButton" : "loginButton save2 save1"}>Login</button>

                                    </div>
                                    <div className="login-footer-text">
                                        alpha ver 0.11
                                        <br />
                                        ©Rimon Hedge Funds LTD
                                    </div>
                                </form>
                            </div>

                            {/* </Modal.Body>
                                </Modal> */}


                        </Col>
                        <Col md={4} />
                    </Row>
                </Grid>
                <NotificationContainer />

            </div >
        );
    }
}

export default Login;