import React, { Component } from "react";
import "react-calendar/dist/Calendar.css";
import "react-datepicker/dist/react-datepicker.css";
import { Grid, Row, Col, Button } from "react-bootstrap";
import axios from "axios";
import config from "constant/config";

class UserDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      filter_profile: "",
      loggedIn: "",
      profiles: [],
      result: [],
    };
  }
  crossClick(e) {
    e.preventDefault();
    this.props.onHide();
  }

  componentDidMount() {
    let data = this.props.data;
    this.allFilterprofiles();
    this.setState({
      username: data.username,
      loggedIn: data.logged_in,
      filter_profile: JSON.parse(data.filter_profile),
    });
    this.setState({})
  }
  allFilterprofiles() {
    let self = this;
    axios
      .get(config.baseurl + "/users/allFilterprofiles")
      .then(function (response) {
        if (response.status == 200) {
          self.setState({ profiles: response.data.rows });
        }
      });
  }
  selectedProfiles() {
    let self = this;
    var matches = [];

    for (var i = 0; i < self.state.filter_profile.length; i++) {
      for (var e = 0; e < self.state.profiles.length; e++) {
        if (self.state.filter_profile[i] === self.state.profiles[e].id)
          matches.push(JSON.stringify(self.state.profiles[e].profile_name)+'  ');
      }
    }
    return matches;
  
  }

  render() {
    return (
      <div className="content">
        <Grid fluid>
          <div className="symbol-box">
            <div className="cross_div">
              <a onClick={(e) => this.crossClick(e)}>
                <i className="fa fa-times" aria-hidden="true"></i>
              </a>
            </div>
            <div className="detail-text">
              <h3> User Details </h3>
            </div>
            <Row>
              <Col md={12}>
                <div className="card">
                  <table className="table mt20">
                    <tbody>
                      <tr>
                        <td className="b-clr">
                          <span>Username </span>
                        </td>

                        <td
                          className="b-clr text-center"
                          style={{ width: "40%" }}
                        >
                          {this.state.username}
                        </td>
                      </tr>
                      <tr>
                        <td className="b-clr">
                          <span>Profile</span>
                        </td>

                        <td
                          className="b-clr text-center"
                          style={{ width: "40%" }}
                        >
                          {this.state.filter_profile ? [ this.selectedProfiles()] :''}
                        </td>
                      </tr>
                      <tr>
                        <td className="b-clr ">
                          <span>Logged In </span>
                        </td>
                        <td
                          className="b-clr text-center"
                          style={{ width: "40%" }}
                        >
                          {this.state.loggedIn === 1 ? "Yes" : "No"}
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </Col>
            </Row>
          </div>
        </Grid>
      </div>
    );
  }
}

export default UserDetail;
