import React, { Component } from "react";
import "react-calendar/dist/Calendar.css";
import "react-datepicker/dist/react-datepicker.css";
import { Grid, Row, Col, Button } from "react-bootstrap";
import DatePicker from "react-datepicker";
import config from "../../constant/config.js";
import Select from "react-select";
import TagsInput from "react-tagsinput";
import "react-tagsinput/react-tagsinput.css";
import { times } from "chartist";
import {
  NotificationContainer,
  NotificationManager,
} from "react-notifications";
const axios = require("axios");

class Innerfilter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedAccountOption: null,
      selectedSymbolOption: null,
      selectedSymbolOptionTo: null,
      selectedAccountOptionTo: null,
      selectedCommissionAccount: null,
      startDate: null,
      endDate: null,
      startDateTo: null,
      endDateTo: null,
      allAccounts: [],
      allSymbols: [],
      profileName: "",
      error: "",
      showCommisoin: false,
      showAddCommission: false,
      autoSyncClose: false,
      profileValues: [],
      nameError: false,
      magicNumber: [],
      magicNumberTo: [],
      showMagicNumber: false,
      showMagicNumberTo: false,
      tags: [],
      include_exclude_status: "",
      exclude: false,
      include: false,
      excludeTo: false,
      includeTo: false,
      ticket: [],
      ticketTo: [],
      excludeTicket: false,
      includeTicket: false,
      excludeTicketTo: false,
      includeTicketTo: false,

      // options: [{ name: 'Srigar', id: 1 }, { name: 'Sam', id: 2 }],

      // plainArray: ["Option 1", "Option 2", "Option 3", "Option 4", "Option 5"],
      // objectArray: [
      //     { key: "Option 1", cat: "Group 1" },
      //     { key: "Option 2", cat: "Group 1" },
      //     { key: "Option 3", cat: "Group 1" },
      //     { key: "Option 4", cat: "Group 2" },
      //     { key: "Option 5", cat: "Group 2" },
      //     { key: "Option 6", cat: "Group 2" },
      //     { key: "Option 7", cat: "Group 2" }
      // ],
      // selectedValues: [
      //     { key: "Option 1", cat: "Group 1" },
      //     { key: "Option 2", cat: "Group 1" }
      // ]
    };
  }
  componentDidMount() {
    this.getSymbolInfo();
    this.fetchfilterdata();
  }
  getSymbolInfo = () => {
    let self = this;
    axios
      .get(config.baseurl + "/accounts/fetchAllAccounts")
      .then(function (response) {
        if (response.status == 200) {
          self.setState({ allAccounts: response.data.rows });
        }
      });
    axios
      .get(config.baseurl + "/accounts/fetchAllSymbol")
      .then(function (response) {
        if (response.status == 200) {
          self.setState({ allSymbols: response.data.rows });
        }
      });
  };
  // addItem = () => {
  //     this.selectedValues.push({ key: "Option 3", cat: "Group 1" });
  // }
  handleDropdownChange = (e) => {
    this.setState({ selectValue: e.target.value });
  };
  handleChangeDate = (date) => {
    this.setState({
      startDate: date,
    });
  };

  handleEndDate = (date) => {
    if (date < this.state.startDate && date != null) {
      this.setState({ error: "enddate", endDate: "" });
      return;
    }
    this.setState({
      error: "",
      endDate: date,
    });
  };
  handleChangeDateTo = (date) => {
    this.setState({
      startDateTo: date,
      error: "",
    });
  };
  handleEndDateTo = (date) => {
    if (date < this.state.startDateTo && date != null) {
      this.setState({ error: "enddateto", endDateTo: "" });
      return;
    }
    this.setState({
      error: "",
      endDateTo: date,
    });
  };
  handleChange = (event) => {
    //this.setState({value: event.option});
    this.setState({
      value: Array.from(event.target.selectedOptions, (item) => item.value),
    });
  };
  renderAccountSelect = () => {
    const { selectedAccountOption, allAccounts } = this.state;
    let Data = {
      account: selectedAccountOption,
    };
    // axios.post(config.baseurl + '/filter/fetchSymbolData', Data)
    //     .then(function (response) {
    //         if (response.status == 200) {
    //             this.props.onHide()
    //         }
    //     })
    //     .catch(function (error) {
    //     });

    let options = allAccounts.map((data) => {
      return { value: data.id, label: data.login };
    });
    return (
      <Select
        placeholder="Select Account"
        value={selectedAccountOption}
        onChange={this.handleChangeAccount}
        options={options}
      />
    );
  };
  handeChangeShowCommission = (e) => {
    this.setState({ showCommission: true }, () => {
      this.setState({ showAddCommission: !this.state.showAddCommission });
    });
  };
  handleChangeAccountTo = (selectedAccountOptionTo) => {
    this.setState({ selectedAccountOptionTo, error: "" });
  };
  renderAccountSelectTo = () => {
    const { selectedAccountOptionTo, allAccounts } = this.state;

    let options = allAccounts.map((data) => {
      return { value: data.id, label: data.login };
    });
    return (
      <Select
        placeholder="Select Account"
        value={selectedAccountOptionTo}
        onChange={this.handleChangeAccountTo}
        options={options}
      />
    );
  };
  handleChangeCommissionAccount = (selectedCommissionAccount) => {
    this.setState({ selectedCommissionAccount });
  };
  renderCommissionAccount = () => {
    const { selectedCommissionAccount, allAccounts } = this.state;

    let options = allAccounts.map((data) => {
      return { value: data.id, label: data.login };
    });
    return (
      <Select
        placeholder="Select Account"
        value={selectedCommissionAccount}
        onChange={this.handleChangeCommissionAccount}
        options={options}
      />
    );
  };
  handleChangeMagicNumber = (magicNumber) => {
    this.setState({ magicNumber });
  };
  handleChangeMagicNumberTo = (magicNumberTo) => {
    this.setState({ magicNumberTo });
  };

  handleChangeTicket = (ticket) => {
    this.setState({ ticket });
  };
  handleChangeTicketTo = (ticketTo) => {
    this.setState({ ticketTo });
  };

  handleChangeAccount = (selectedAccountOption) => {
    this.setState({ selectedAccountOption });
  };
  handleChangeSymbol = (selectedSymbolOption) => {
    this.setState({ selectedSymbolOption });
  };
  renderSymbolSelect = () => {
    const { selectedSymbolOption, allSymbols } = this.state;
    // console.log(selectedSymbolOption, allSymbols, "selectedSymbolOption, allSymbols--------->")
    let options = allSymbols.map((data) => {
      return { value: data.name, label: data.name };
    });
    return (
      <Select
        placeholder="Select Symbol"
        value={selectedSymbolOption}
        isMulti
        onChange={this.handleChangeSymbol}
        options={options}
      />
    );
  };
  handleChangeSymbolTo = (selectedSymbolOptionTo) => {
    this.setState({ selectedSymbolOptionTo });
  };
  renderSymbolSelectTo = () => {
    const { selectedSymbolOptionTo, allSymbols } = this.state;
    let options = allSymbols.map((data) => {
      return { value: data.name, label: data.name };
    });

    return (
      <Select
        placeholder="Select Symbol..."
        value={selectedSymbolOptionTo}
        isMulti
        onChange={this.handleChangeSymbolTo}
        options={options}
      />
    );
  };
  fetchfilterdata() {
    let self = this;
    let token = JSON.parse(localStorage.getItem("accessToken"));
    axios
      .get(config.baseurl + "/filter/fetchfilterdata?page=1", { headers: { 'x-access-token': token } })
      .then(function (response) {
        if (response.status == 200) {
          self.setState({ profileValues: response.data.rows });
        }
      });
  }

  handleprofileName(e) {
      this.setState({ nameError: false });
      if (this.state.profileValues.length <= 0) {
        this.setState({
        profileName:
          e.target.value.charAt(0).toUpperCase() + e.target.value.slice(1),
        error: "",
      });
    } else {
      this.state.profileValues.filter((data) => {
         if (data.profile_name.toUpperCase() !== e.target.value.toUpperCase()) {
          this.setState({profileName: e.target.value.charAt(0).toUpperCase() + e.target.value.slice(1),
            error: "",
          });
        } else {
          this.setState({ nameError: true }, () => {});
          NotificationManager.listNotify = [];
          NotificationManager.error("Profile already exits!");
        }
      });
    }
  }
  // handlemagicNumber(e){
  //     this.setState({ magicNumber: e.target.value})
  // }

  // handlemagicNumberTo(e){
  //     this.setState({ magicNumberTo: e.target.value})
  // }

  formDataSubmit(e) {
    e.preventDefault();

    let from_status =
      this.state.include === true && this.state.exclude === false
        ? 1
        : this.state.exclude === true && this.state.include === false
        ? 2
        : 0;

    let to_status =
      this.state.includeTo === true && this.state.excludeTo === false
        ? 1
        : this.state.excludeTo === true && this.state.includeTo === false
        ? 2
        : 0;

    let from_status_ticket =
      this.state.includeTicket === true && this.state.excludeTicket === false
        ? 1
        : this.state.excludeTicket === true &&
          this.state.includeTicket === false
        ? 2
        : 0;

    let to_status_ticket =
      this.state.includeTicketTo === true &&
      this.state.excludeTicketTo === false
        ? 1
        : this.state.excludeTicketTo === true &&
          this.state.includeTicketTo === false
        ? 2
        : 0;

    let self = this;

    if (this.state.profileName == null || this.state.profileName == "") {
      NotificationManager.listNotify = [];
      NotificationManager.error("Profile name is required !");
      return;
    } else if (
      this.state.selectedAccountOption == null ||
      this.state.selectedAccountOption == ""
    ) {
      this.setState({ error: "accountoption" });
      return;
    } else if (
      this.state.showCommission == true &&
      (this.state.selectedCommissionAccount == null ||
        this.state.selectedCommissionAccount == "")
    ) {
      this.setState({ error: "selectedCommissionAccount" });
      return;
    } else if (this.state.startDate == null || this.state.startDate == "") {
      this.setState({ error: "startdate" });
      return;
    } else if (
      this.state.selectedSymbolOption == null ||
      this.state.selectedSymbolOption == ""
    ) {
      this.setState({ error: "symbolselect" });
      return;
    } else if (
      this.state.selectedAccountOptionTo == null ||
      this.state.selectedAccountOptionTo == ""
    ) {
      this.setState({ error: "accountoptionto" });
      return;
    } else if (this.state.startDateTo == null || this.state.startDateTo == "") {
      this.setState({ error: "startDateTo" });
      return;
    } else if (
      this.state.selectedSymbolOptionTo == null ||
      this.state.selectedSymbolOptionTo == ""
    ) {
      this.setState({ error: "selectedSymbolOptionTo" });
      return;
    } else if (this.state.nameError) {
      NotificationManager.listNotify = [];
      NotificationManager.error("Profile Name Already Exist!");
    } else {
      this.setState({ error: " " });
      let fromSymbol = this.state.selectedSymbolOption;
      let symbolFrom = fromSymbol.map((data) => {
        return data.value;
      });
      let toSymbol = this.state.selectedSymbolOptionTo;
      let symbolto = toSymbol.map((data) => {
        return data.value;
      });
      // console.log(this.state.selectedCommissionAccount, 'this.state.selectedCommissionAccount')

      let Data = {
        profile_name: this.state.profileName,
        from_account_id: this.state.selectedAccountOption.value,
        to_account_id: this.state.selectedAccountOptionTo.value,
        commission_acount_id: this.state.showAddCommission
          ? this.state.selectedCommissionAccount.value
          : 0,
        startdateFrom: this.state.startDate,
        enddateFrom: this.state.endDate,
        startdateTo: this.state.startDateTo,
        enddateTo: this.state.endDateTo,
        from_symbols: JSON.stringify(symbolFrom),
        to_symbols: JSON.stringify(symbolto),
        auto_close: this.state.autoSyncClose ? 1 : 0,
        from_magic_number:
          from_status != 0
            ? this.state.magicNumber.length > 0
              ? JSON.stringify(this.state.magicNumber)
              : JSON.stringify("")
            : "",
        to_magic_number:
          to_status != 0
            ? this.state.magicNumberTo.length > 0
              ? JSON.stringify(this.state.magicNumberTo)
              : JSON.stringify("")
            : "",
        from_include_exclude_status: from_status,
        to_include_exclude_status: to_status,
        from_ticket:
          from_status_ticket != 0
            ? this.state.ticket.length > 0
              ? JSON.stringify(this.state.ticket)
              : JSON.stringify("")
            : "",
        to_ticket:
          to_status_ticket != 0
            ? this.state.ticketTo.length > 0
              ? JSON.stringify(this.state.ticketTo)
              : JSON.stringify("")
            : "",
        from_include_exclude_status_ticket: from_status_ticket,
        to_include_exclude_status_ticket: to_status_ticket,
        createdAt: new Date(),
        updatedAt: new Date(),
      };
    //   console.log(Data, "dat-------------");

      axios
        .post(config.baseurl + "/filter/addfilterdata", Data)
        .then(function (response) {
          if (response.status == 200) {
            self.props.onHide();
          }
        })
        .catch(function (error) {});
    }
  }
  crossClick(e) {
    e.preventDefault();
    this.props.onHide();
  }
  handleChangeAutoSync(e) {
    this.setState({ autoSyncClose: !this.state.autoSyncClose }, () => {});
  }

  changeIncludeDefaultProperty = () => {
    this.setState({ include: !this.state.include, exclude: false });
    // if (!this.state.include == true) {
    //     this.setState({ showMagicNumber: true })
    // } else if (this.state.include == true) {
    //     this.setState({ showMagicNumber: false })

    // }
  };
  changeExcludeDefaultProperty = () => {
    this.setState({ exclude: !this.state.exclude, include: false });
    // if (!this.state.exclude == true) {
    //     this.setState({ showMagicNumber: true })
    // } else if (this.state.exclude == true) {
    //     this.setState({ showMagicNumber: false })

    // }
  };

  changeIncludeDefaultPropertyTo = () => {
    this.setState({ includeTo: !this.state.includeTo, excludeTo: false });
    // if (!this.state.includeTo == true) {
    //     this.setState({ showMagicNumberTo: true })
    // } else if (this.state.includeTo == true) {
    //     this.setState({ showMagicNumberTo: false })

    // }
  };
  changeExcludeDefaultPropertyTo = () => {
    this.setState({ excludeTo: !this.state.excludeTo, includeTo: false });
    // if (!this.state.excludeTo == true) {
    //     this.setState({ showMagicNumberTo: true })
    // } else if (this.state.excludeTo == true) {
    //     this.setState({ showMagicNumberTo: false })

    // }
  };
  changeIncludeDefaultPropertyTicket = () => {
    this.setState({
      includeTicket: !this.state.includeTicketTo,
      excludeTicket: false,
    });
  };
  changeExcludeDefaultPropertyTicket = () => {
    this.setState({
      excludeTicket: !this.state.excludeTicket,
      includeTicket: false,
    });
  };

  changeIncludeDefaultPropertyTicketTo = () => {
    this.setState({
      includeTicketTo: !this.state.includeTicketTo,
      excludeTicketTo: false,
    });
  };
  changeExcludeDefaultPropertyTicketTo = () => {
    this.setState({
      excludeTicketTo: !this.state.excludeTicketTo,
      includeTicketTo: false,
    });
  };
  render() {
    let s = this;
    return (
      <>
        <NotificationContainer />
        <div className="content">
          <Grid fluid>
            <div className="symbol-box">
              <div className="cross_div">
                <a onClick={(e) => this.crossClick(e)}>
                  <i className="fa fa-times" aria-hidden="true"></i>
                </a>
              </div>
              <div className="terminal-text">
                <label className="pnameadjust"> Profile Name </label>
                <input type="text" name="profilename" className="Itext" placeholder="Profile Name" onChange={(e) => this.handleprofileName(e)} />
              </div>
              <div className="synclose">
                <label className="checkbox-inline">
                  <input
                    type="checkbox"
                    defaultChecked={this.state.autoSyncClose}
                    name="changeAutoSync"
                    onChange={(e) => this.handleChangeAutoSync(e)}
                  />
                  <b>Auto Sync Close</b>
                </label>
              </div>

              <form>
                <Row>
                  <Col md={6}>
                    <div className="card bottom">
                      <div style={{ padding: "10px 20px 0px" }}>
                        <span style={{ fontWeight: "bold" }}>Account A</span>
                      </div>
                      <table className="table mt20">
                        <tbody>
                          <tr>
                            <td className="b-clr">
                              <span className="accnt">Account </span>
                            </td>

                            <td
                              className="b-clr text-center"
                              style={{ width: "50%" }}
                            >
                              {/* <Multiselect
                                                        options={objectArray}
                                                        singleSelect
                                                        displayValue="key"
                                                    /> */}
                              {this.renderAccountSelect()}
                              {this.state.error == "accountoption" && (
                                <p style={{ color: "red", fontSize: "13px" }}>
                                  *Please Select Your Account
                                </p>
                              )}
                            </td>
                          </tr>
                          <tr>
                            <td className="b-clr ">
                              <span className="accnt">From </span>
                            </td>
                            <td className="b-clr text-center datepicker_filter">
                              <DatePicker
                                selected={this.state.startDate}
                                onChange={this.handleChangeDate}
                                isClearable
                                placeholderText="dd/mm/yyyy"
                                dateFormat="dd/MM/yyyy"
                                startDate={this.state.startDate}
                                endDate={this.state.endDate}
                                strictParsing
                              />
                              {/* <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                                            <KeyboardDatePicker
                                                                // margin="normal"
                                                                clearable={false}
                                                                id="date-picker-dialog"
                                                                label="dd/MM/yyyy"
                                                                format="dd/MM/yyyy"
                                                                onError={() => { return null }}
                                                                value={this.state.startDate}
                                                                onChange={this.handleChangeDate}
                                                                KeyboardButtonProps={{
                                                                    'aria-label': 'change date',
                                                                }}
                                                            />
                                                        </MuiPickersUtilsProvider> */}
                              {this.state.error == "startdate" && (
                                <p style={{ color: "red", fontSize: "13px" }}>
                                  *Please Select Start Date
                                </p>
                              )}
                            </td>
                          </tr>
                          <tr>
                            <td className="b-clr ">
                              <span className="accnt">To </span>
                            </td>
                            <td className="b-clr text-center datepicker_filter">
                              <DatePicker
                                autoOk={true}
                                selected={this.state.endDate}
                                onChange={this.handleEndDate}
                                isClearable
                                placeholderText="dd/mm/yyyy"
                                dateFormat="dd/MM/yyyy"
                                startDate={this.state.startDate}
                                endDate={this.state.endDate}
                              />
                              {this.state.error == "enddate" && (
                                <p style={{ color: "red", fontSize: "13px" }}>
                                  * Please ensure that the End Date is greater
                                  than the Start Date.
                                </p>
                              )}
                            </td>
                          </tr>
                          <tr>
                            <td className="b-clr ">
                              <span className="accnt">Symbol </span>
                            </td>
                            <td
                              className="b-clr text-center"
                              style={{ width: "40%" }}
                            >
                              {/* <Multiselect
                                                        options={objectArray}
                                                        displayValue="key"
                                                        showCheckbox={true}
                                                    /> */}
                              {this.renderSymbolSelect()}
                              {this.state.error == "symbolselect" && (
                                <p style={{ color: "red", fontSize: "13px" }}>
                                  *Please Select Your Symbol{" "}
                                </p>
                              )}
                            </td>
                          </tr>

                          <tr>
                            <td className="b-clr ">
                              <span className="accnt">Magic Number </span>
                            </td>
                            <td
                              className="b-clr text-center"
                              style={{ width: "40%" }}
                            >
                              <TagsInput
                                onValidationReject={() =>
                                  console.log("rejected")
                                }
                                validationRegex={/^[0-9]*$/}
                                inputProps={{
                                  placeholder:
                                    this.state.magicNumber.length > 0
                                      ? ""
                                      : " Enter Magic Number",
                                }}
                                type="number"
                                value={this.state.magicNumber}
                                onChange={this.handleChangeMagicNumber}
                              ></TagsInput>
                            </td>
                          </tr>
                          <tr>
                            <td className="b-clr dataAdjust">
                              <div onClick={this.changeIncludeDefaultProperty}>
                                <input
                                  type="radio"
                                  id="include"
                                  name="include"
                                  checked={this.state.include}
                                />
                                <span className="accnt ">Include</span>
                              </div>
                            </td>
                            <td className="b-clr text-center">
                              <div onClick={this.changeExcludeDefaultProperty}>
                                <input
                                  type="radio"
                                  id="exclude"
                                  name="exclude"
                                  checked={this.state.exclude}
                                />
                                <span className="accnt ">Exclude</span>
                              </div>
                            </td>
                          </tr>
                          <tr>
                            <td className="b-clr ">
                              <span className="accnt">Ticket </span>
                            </td>
                            <td
                              className="b-clr text-center"
                              style={{ width: "40%" }}
                            >
                              <TagsInput
                                onValidationReject={() =>
                                  console.log("rejected")
                                }
                                validationRegex={/^[0-9]*$/}
                                inputProps={{
                                  placeholder:
                                    this.state.ticket.length > 0
                                      ? ""
                                      : " Enter Ticket",
                                }}
                                type="number"
                                value={this.state.ticket}
                                onChange={this.handleChangeTicket}
                              ></TagsInput>
                            </td>
                          </tr>

                          <tr>
                            <td className="b-clr dataAdjust">
                              <div
                                onClick={
                                  this.changeIncludeDefaultPropertyTicket
                                }
                              >
                                <input
                                  type="radio"
                                  id="includeTicket"
                                  name="includeTicket"
                                  checked={this.state.includeTicket}
                                />
                                <span className="accnt ">Include</span>
                              </div>
                            </td>
                            <td className="b-clr text-center">
                              <div
                                onClick={
                                  this.changeExcludeDefaultPropertyTicket
                                }
                              >
                                <input
                                  type="radio"
                                  id="excludeTicket"
                                  name="excludeTicket"
                                  checked={this.state.excludeTicket}
                                />
                                <span className="accnt ">Exclude</span>
                              </div>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="card bottom">
                      <div style={{ padding: "10px 20px 0px" }}>
                        <span style={{ fontWeight: "bold" }}>Account B</span>
                      </div>
                      <table className="table mt20">
                        <tbody>
                          <tr>
                            <td className="b-clr ">
                              <span className="accnt"> Account</span>
                            </td>
                            <td
                              className="b-clr text-center"
                              style={{ width: "50%" }}
                            >
                              {this.renderAccountSelectTo()}
                              {this.state.error == "accountoptionto" && (
                                <p style={{ color: "red", fontSize: "13px" }}>
                                  *Please Select Your Account
                                </p>
                              )}
                            </td>
                          </tr>
                          <tr>
                            <td className="b-clr ">
                              <span className="accnt">From </span>
                            </td>
                            <td className="b-clr text-center datepicker_filter">
                              <DatePicker
                                selected={this.state.startDateTo}
                                onChange={this.handleChangeDateTo}
                                isClearable
                                placeholderText="dd/mm/yyyy"
                                dateFormat="dd/MM/yyyy"
                                startDate={this.state.startDateTo}
                                endDate={this.state.endDateTo}
                                strictParsing
                              />
                              {this.state.error == "startDateTo" && (
                                <p style={{ color: "red", fontSize: "13px" }}>
                                  *Please Select Start Date
                                </p>
                              )}
                            </td>
                          </tr>
                          <tr>
                            <td className="b-clr ">
                              <span className="accnt">To </span>
                            </td>
                            <td className="b-clr text-center datepicker_filter">
                              <DatePicker
                                selected={this.state.endDateTo}
                                onChange={this.handleEndDateTo}
                                isClearable
                                placeholderText="dd/mm/yyyy"
                                dateFormat="dd/MM/yyyy"
                                startDate={this.state.startDateTo}
                                endDate={this.state.endDateTo}
                              />
                              {this.state.error == "enddateto" && (
                                <p style={{ color: "red", fontSize: "13px" }}>
                                  * Please ensure that the End Date is greater
                                  than the Start Date.
                                </p>
                              )}
                            </td>
                          </tr>
                          <tr>
                            <td className="b-clr">
                              <span className="accnt"> Symbol </span>
                            </td>
                            <td
                              className="b-clr text-center"
                              style={{ width: "40%" }}
                            >
                              {/* <Multiselect
                                                        options={objectArray}
                                                        displayValue="key"
                                                        showCheckbox={true}
                                                    /> */}
                              {this.renderSymbolSelectTo()}
                              {this.state.error == "selectedSymbolOptionTo" && (
                                <p style={{ color: "red", fontSize: "13px" }}>
                                  *Please Select Your Symbol{" "}
                                </p>
                              )}
                            </td>
                          </tr>

                          <tr>
                            <td className="b-clr ">
                              <span className="accnt">Magic Number </span>
                            </td>
                            <td
                              className="b-clr text-center"
                              style={{ width: "40%" }}
                            >
                              <TagsInput
                                onValidationReject={() =>
                                  console.log("rejected")
                                }
                                validationRegex={/^[0-9]*$/}
                                inputProps={{
                                  placeholder:
                                    this.state.magicNumberTo.length > 0
                                      ? ""
                                      : " Enter Magic Number",
                                }}
                                type="number"
                                value={this.state.magicNumberTo}
                                onChange={this.handleChangeMagicNumberTo}
                              ></TagsInput>
                            </td>
                          </tr>
                          <tr>
                            <td className="b-clr dataAdjust">
                              <div
                                onClick={this.changeIncludeDefaultPropertyTo}
                              >
                                <input
                                  type="radio"
                                  id="includeTo"
                                  name="includeTo"
                                  checked={this.state.includeTo}
                                />
                                <span className="accnt ">Include</span>
                              </div>
                            </td>
                            <td className="b-clr text-center">
                              <div
                                onClick={this.changeExcludeDefaultPropertyTo}
                              >
                                <input
                                  type="radio"
                                  id="excludeTo"
                                  name="excludeTo"
                                  checked={this.state.excludeTo}
                                />
                                <span className="accnt ">Exclude</span>
                              </div>
                            </td>
                          </tr>
                          <tr>
                            <td className="b-clr ">
                              <span className="accnt">Ticket </span>
                            </td>
                            <td
                              className="b-clr text-center"
                              style={{ width: "40%" }}
                            >
                              <TagsInput
                                onValidationReject={() =>
                                  console.log("rejected")
                                }
                                validationRegex={/^[0-9]*$/}
                                inputProps={{
                                  placeholder:
                                    this.state.ticketTo.length > 0
                                      ? ""
                                      : " Enter Ticket",
                                }}
                                type="number"
                                value={this.state.ticketTo}
                                onChange={this.handleChangeTicketTo}
                              ></TagsInput>
                            </td>
                          </tr>
                          <tr>
                            <td className="b-clr dataAdjust">
                              <div
                                onClick={
                                  this.changeIncludeDefaultPropertyTicketTo
                                }
                              >
                                <input
                                  type="radio"
                                  id="includeTicketTo"
                                  name="includeTicketTo"
                                  checked={this.state.includeTicketTo}
                                />
                                <span className="accnt ">Include</span>
                              </div>
                            </td>
                            <td className="b-clr text-center">
                              <div
                                onClick={
                                  this.changeExcludeDefaultPropertyTicketTo
                                }
                              >
                                <input
                                  type="radio"
                                  id="excludeTicketTo"
                                  name="excludeTicketTo"
                                  checked={this.state.excludeTicketTo}
                                />
                                <span className="accnt ">Exclude</span>
                              </div>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </Col>
                </Row>
                <hr></hr>
                <div className="chceckbox">
                  <label className="checkbox-inline">
                    <input
                      type="checkbox"
                      defaultChecked={this.state.showCommisoin}
                      onChange={(e) => this.handeChangeShowCommission(e)}
                    />
                    <b>Add Commission</b>
                  </label>
                </div>
                {this.state.showAddCommission && (
                  <div className="center-com d-flex">
                    <div className="com">
                      <h3 style={{ fontWeight: "bold" }}>Commission</h3>
                    </div>
                    <table className="table mt20">
                      <tbody>
                        <tr>
                          <td className="b-clr ">
                            <span className="accnt"> Account </span>
                          </td>
                          <td
                            className="b-clr text-center"
                            style={{ width: "50%" }}
                          >
                            {this.renderCommissionAccount()}

                            {this.state.error ==
                              "selectedCommissionAccount" && (
                              <p style={{ color: "red", fontSize: "13px" }}>
                                *Please Select Your Account{" "}
                              </p>
                            )}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                )}
                <div className="save_button_filter">
                  <div className="savebtn">
                    <Button
                      className="btn-primary savebutton"
                      variant="primary"
                      size="lg"
                      block
                      onClick={(e) => this.formDataSubmit(e)}
                    >
                      <b>Save</b>
                    </Button>
                  </div>
                </div>
              </form>
            </div>
          </Grid>
        </div>
      </>
    );
  }
}

export default Innerfilter;
