import React, { useState, useEffect } from "react";
import { Container, Col, Row, Label } from "reactstrap";
import Loader from "react-loader-spinner";
import myInitObject from "../../api";
import DatatablePage from "views/DatatablePage";
import DatePicker from "react-datepicker";
import Select from "react-select";
// import "react-select/dist/react-select.css";
const moment = require("moment");
const Swap = () => {
  const [dailySwap, setdailySwap] = useState([]);
  const [loading, setloading] = useState([]);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [account, setAccount] = useState([]);
  const [symbol, setSymbol] = useState([]);
  const [act, setAct] = useState("");
  const [sym, setSym] = useState("");
  const [newResult, setResult] = useState([]);
  const [filterResult, setFilterResult] = useState([]);
  const [filtered, setFiltered] = useState(false);
  const [dateTime, setdateTime] = useState("");
  const [activeProfileName, setactiveProfileName] = useState("");

  useEffect(() => {
    setInterval(() => {
      if (myInitObject.responsLUT &&
        myInitObject.responsDS &&
        myInitObject.responsAC &&
        myInitObject.responsAS &&
        myInitObject.responsAF === undefined
      ) {
        console.log("no data");
      } else { 
        setdateTime(myInitObject.fetchLastUpdatedTime.filteredInfoTime);
        setloading(myInitObject.fetchdailyswaps === 0 ? true : false);
        setdailySwap(myInitObject.fetchdailyswaps.rows);
        setSymbol(myInitObject.fetchAllSymbol.rows);
        setAccount(myInitObject.fetchAllAccounts.rows);
        setResult(myInitObject.fetchdailyswaps.rows);
        setactiveProfileName(myInitObject.fetchActivefilterdata.rows[0].profile_name);
      }
    }, 1000);
  }, []);
  // useEffect(() => {
  //   let sd = moment(startDate).format("DD-MM-yyyy");
  //   let ed = moment(endDate).format("DD-MM-yyyy");
  //   let result = dailySwap.filter((d) => {
  //     var time = moment(d.date).format("DD-MM-yyyy");
  //     if (sd === "Invalid date" && ed === "Invalid date") {
  //       return dailySwap;
  //     } else if (sd !== "Invalid date" && ed === "Invalid date") {
  //       return time >= sd;
  //     } else if (sd === "Invalid date" && ed !== "Invalid date") {
  //       return time <= ed;
  //     } else if (time >= sd && time <= ed) {
  //       return d;
  //     }
  //     //  return (sd <= time && time <= ed);
  //   });
  //   setFilterResult(result);
  // }, [dailySwap]);
  const dateFilter = (date, method) => {
    console.log(date);
    let sd, ed;
    sd = startDate ? startDate : "";
    ed = endDate ? endDate : "";
    if (method === "start") {
      console.log(startDate);

      sd = date ? moment(date).format("MM-DD-yyyy") : date;
      setStartDate(date);
    }
    if (method === "end") {
      console.log(startDate);
      ed = date ? moment(date).format("MM-DD-yyyy") : date;

      setEndDate(date);
    }

    // sd = moment(sd).isSameOrBefore(ed, 'day') ? sd : ed;
    // ed = moment(ed).isSameOrAfter(sd, 'day') ? ed : sd;

    let result = dailySwap.filter((d) => {
      let time = moment(d.date).format("MM-DD-yyyy");
      // if (sd === "Invalid date" && ed === "Invalid date") {
      //   return dailySwap;
      // } else if (sd !== "Invalid date" && ed === "Invalid date") {
      //   return time >= sd;
      // } else if (sd === "Invalid date" && ed !== "Invalid date") {
      //   return time <= ed;
      // } else if (time >= sd && time <= ed) {
      //   return d;
      // }
      if (sd == false && ed == false) {
        return true;
      } else if (sd != false && ed == false) {
        return moment(time).isSameOrAfter(sd, "day");
        // return time >= sd;
      } else if (sd == false && ed != false) {
        return moment(time).isSameOrBefore(ed, "day");
        // return time <= ed;
      } else if (sd != false && ed != false) {
        return (
          moment(time).isSameOrAfter(sd, "day") &&
          moment(time).isSameOrBefore(ed, "day")
        );
      }
      //  return (sd <= time && time <= ed);
    });
    console.log(sd, ed, result);
    setFilterResult(result, setFiltered(true));
  };
  const loginData = (paramsact) => {
    setAct(paramsact);
    let result = newResult.filter(
      (d) => d.account_login === String(paramsact.value) && true
    );
    setFilterResult(result, setFiltered(true));
  };
  const symbolData = (paramssym) => {
    setSym(paramssym);
    let result = newResult.filter((d) => d.symbol === paramssym.value && true);
    setFilterResult(result, setFiltered(true));
  };

  const SymbolSelect = () => {
    let options = symbol.map((data) => {
      return { value: data.name, label: data.name };
    });

    return (
      <Select
        placeholder="Select Symbol..."
        value={sym}
        onChange={symbolData}
        options={options}
      />
    );
  };
  const AccountSelect = () => {
    let options = account.map((data) => {
      return { value: data.login, label: data.login };
    });

    return (
      <Select
        placeholder="Select Account..."
        value={act}
        onChange={loginData}
        options={options}
      />
    );
  };
  // console.log(filterResult,  "filterResult");
  return (
    <Container fluid>
       <Row className="no-margin">
            <div className="lastUpdate">
              <span> Last Updated : </span>
              {dateTime}(UTC)
            </div>
          </Row>
      <div className="pad-y">
        <div className="symbol-box" style={{ marginBottom: "20px" }}>
          <div className="openpositions">
            <span className="p-text">Daily Swaps</span>
          </div>
        </div>
        {loading ? (
          <Loader
            type="ThreeDots"
            color="black"
            height={100}
            width={100}
            style={{ textAlign: "center" }}
          />
        ) : (
          <div className="symbol-box">
            <Row>
              <Col md={6}>
                <label>Login</label>
                {AccountSelect()}
              </Col>
              <Col md={6}>
                <label>Symbol</label>
                {SymbolSelect()}
              </Col>
            </Row>
            <Row>
              <Col md={12} className="filter-date">
                <Label>Start Date</Label>
                <DatePicker
                  selected={startDate}
                  onChange={(date) => {
                    dateFilter(date, "start");
                  }}
                  selectsStart
                  placeholderText="mm-dd-yyyy"
                  dateFormat="MM-dd-yyyy"
                  startDate={startDate}
                  endDate={endDate}
                />
                <Label>End Date</Label>
                <DatePicker
                  selected={endDate}
                  onChange={(date) => {
                    dateFilter(date, "end");
                  }}
                  selectsEnd
                  placeholderText="mm-dd-yyyy"
                  dateFormat="MM-dd-yyyy"
                  startDate={startDate}
                  endDate={endDate}
                  minDate={startDate}
                />
              </Col>
            </Row>
            <DatatablePage
              data={filtered === true ? filterResult : newResult}
            />
          </div>
        )}
      </div>
    </Container>
  );
};

export default Swap;
