import React from 'react';
import { MDBDataTable } from 'mdbreact';
const moment = require("moment");

const DatatablePage = (props) => {
    let data = {
        columns: [
            {
                label: 'Date',
                field: 'date',
                sort: 'asc',
                width: 100
            },
            {
                label: 'Login',
                field: 'account_login',
                sort: 'asc',
                width: 100
            },
            {
                label: 'Symbol',
                field: 'symbol',
                sort: 'asc',
                width: 100
            },
            {
                label: 'Long',
                field: 'swap_long',
                sort: 'asc',
                width: 100
            },
            {
                label: 'Short',
                field: 'swap_short',
                sort: 'asc',
                width: 100
            },
            
        ],
        
        rows: props.data.map(data => {
            data.date = moment(data.date).format('L');

            data.swap_long = Number.parseFloat(data.swap_long).toFixed(2);
            data.swap_short = Number.parseFloat(data.swap_short).toFixed(2);
            
            return data
        }
        )
    };
    return <MDBDataTable responsive bordered small data={data} />;
}
export default DatatablePage;