import React, { Component } from "react";
import "react-calendar/dist/Calendar.css";
import { Grid, Row, Col, Modal } from "react-bootstrap";
import Innerfilter from "../filter/innerfilter";
import FilterEdite from "../filter/filteredit";
import FilterDetail from "../filter/filterdetail";
import "react-datepicker/dist/react-datepicker.css";
import config from "../../constant/config.js";
import {
  NotificationContainer,
  NotificationManager,
} from "react-notifications";
import "react-notifications/lib/notifications.css";
import { api } from "api";
import ReactPaginate from "react-paginate";
import myInitObject from "../../api";

const axios = require("axios");
var moment = require("moment");

class Filterprofile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Innerfilter: false,
      filter: true,
      filterData: [],
      deleteData: [],
      editeData: [],
      showDelete: false,
      showEdit: false,
      detailForm: false,
      detailData: [],
      count: "",
      pages: "",
      page: 1,
      dateTime:'',
    };

    // this.handleDropdownChange = this.handleDropdownChange.bind(this);
  }
  fetchfilterdata() {
    let token = JSON.parse(localStorage.getItem("accessToken"));
    let self = this;
    axios
      .get(config.baseurl + `/filter/fetchfilterdata?page=${self.state.page}`, {
        headers: { "x-access-token": token },
      })
      .then(function (response) {
        if (response.status == 200) {
          self.setState({
            filterData: response.data.rows,
            count: response.data.count,
            pages: response.data.pages,
          });
        }
      });
  }
  handlePageClick = (data) => {
    this.setState({ page: data.selected + 1 }, () => {
      this.fectheAllAccount(this.state.page);
    });
  };
  showInnerfilter = () => {
    this.setState({ Innerfilter: true, filter: false });
  };
  componentDidMount() {
    this.fetchfilterdata();
    setInterval(() => {
      if (myInitObject.responsLUT === undefined) {
        console.log("no data");
      } else {
        this.setState({
          //last update time
          dateTime: myInitObject.fetchLastUpdatedTime.filteredInfoTime,
        });
      }
    }, 1000);
    // let self = this;
    // axios.get(config.baseurl + '/filter/fetchfilterdata')
    //     .then(function (response) {
    //         if (response.status == 200) {
    //             self.setState({ filterData: response.data.rows })
    //         }
    //     }) adfasdfsd
  }

  createNotification = (profile_name, type) => {
    NotificationManager.listNotify = [];
    NotificationManager.info("Profile Is Activated !", profile_name);
  };

  createWariningNotification = (profile_name, type) => {
    NotificationManager.listNotify = [];
    NotificationManager.error("Profile Is Deactivated !", profile_name);
  };

  activateClick(e, data) {
    e.preventDefault();
    let token = JSON.parse(localStorage.getItem("accessToken"));
    let Data = {
      id: data.id,
      status: 1,
    };
    let self = this;
    axios
      .post(config.baseurl + "/filter/updatefilterdata", Data, {
        headers: { "x-access-token": token },
      })
      .then(function (response) {
        if (response.status == 200) {
          self.fetchfilterdata();
          self.createNotification(data.profile_name, "info");
          // self.setState({ filterData: response.data.rows })
          api();
        }
      });
  }

  //  App(){
  //     const notify = () => toast("Wow so easy !");

  //     return (
  //       <div>
  //         <button onClick={notify}>Notify !</button>
  //         <ToastContainer />
  //       </div>
  //     );
  //   }

  deactivateClick(e, data) {
    e.preventDefault();
    let token = JSON.parse(localStorage.getItem("accessToken"));
    let Data = {
      id: data.id,
      status: 0,
    };
    let self = this;
    axios
      .post(config.baseurl + "/filter/updatefilterdata", Data, {
        headers: { "x-access-token": token },
      })
      .then(function (response) {
        if (response.status == 200) {
          self.fetchfilterdata();
          self.createWariningNotification(data.profile_name, "error");
          // self.setState({ filterData: response.data.rows })
        }
      });
  }
  editFilter = (e, data) => {
    // e.preventDefault();
    if (this.state.endDate == null || this.state.endDate == "") {
      let newDate = new Date();
      this.setState({ endDate: newDate });
      return;
    }
    this.setState({ showEdit: true, editeData: data });
  };

  deleteFilter(e, data) {
    e.preventDefault();
    this.setState({ deleteData: data, showDelete: true });
  }
  deletepopupClick(e) {
    e.preventDefault();
    let Data = {
      id: this.state.deleteData.id,
    };
    let self = this;
    axios
      .post(config.baseurl + "/filter/deletefilter", Data)
      .then(function (response) {
        if (response.status == 200) {
          self.setState({ showDelete: false });
          self.fetchfilterdata();
        }
      });
  }
  deletepopupclose(e) {
    e.preventDefault();
    this.setState({ showDelete: false });
  }
  viewFilter(e, data) {
    e.preventDefault();

    this.setState({ detailForm: true, detailData: data });
  }

  createTr = () => {
    return (
      this.state.filterData.length > 0 &&
      this.state.filterData.map((item, index) => {
        return (
          <tr key={index}>
            <th scope="row">{index + 1}</th>
            <td>{item.profile_name}</td>
            <td>{moment(item.createdAt).format("DD/MM/YYYY")}</td>
            {/* <td>{item.status == 1 ? 'Activated' : 'Deactivated' }</td> */}
            <td>
              {JSON.parse(localStorage.getItem("currentUser"))[0].role ===
              "user" ? (
                <>
                  {item.user_status == 0 ? (
                    <div className="activate_button pointer">
                      <span
                        className="button_interactivate btcolor"
                        onClick={(e) => this.activateClick(e, item)}
                      >
                        {" "}
                        Activate{" "}
                      </span>{" "}
                    </div>
                  ) : (
                    <div className="deactivate_button pointer">
                      <span
                        className="button_deactivate pointer"
                        onClick={(e) => this.deactivateClick(e, item)}
                      >
                        Deactivate
                      </span>
                    </div>
                  )}
                </>
              ) : (
                <>
                  {item.status == 0 ? (
                    <div className="activate_button pointer">
                      <span
                        className="button_interactivate btcolor"
                        onClick={(e) => this.activateClick(e, item)}
                      >
                        {" "}
                        Activate{" "}
                      </span>{" "}
                    </div>
                  ) : (
                    <div className="deactivate_button pointer">
                      <span
                        className="button_deactivate pointer"
                        onClick={(e) => this.deactivateClick(e, item)}
                      >
                        Deactivate
                      </span>
                    </div>
                  )}
                </>
              )}
            </td>
            <td>
              <span onClick={(e) => this.viewFilter(e, item)}>
                <i className="pointer fa fa-eye facolor" aria-hidden="true"></i>
              </span>
              {JSON.parse(localStorage.getItem("currentUser"))[0].role !==
                "user" && (
                <>
                  <span onClick={(e) => this.editFilter(e, item)}>
                    <i
                      className="pointer fa fa-pencil-square-o facolor"
                      aria-hidden="true"
                    ></i>
                  </span>
                  {item.status == 0 && (
                    <span onClick={(e) => this.deleteFilter(e, item)}>
                      <i
                        className="pointer fa fa-trash facolor"
                        aria-hidden="true"
                      ></i>
                    </span>
                  )}
                </>
              )}
            </td>
            {/* <td>{item.accountToInfo[0].login}</td>
                    <td>{moment(item.startdateFrom).format("DD/MM/YYYY") + ' - ' + moment(item.enddateFrom).format("DD/MM/YYYY")}</td>
                    <td>{moment(item.startdateTo).format("DD/MM/YYYY") + ' - ' + moment(item.enddateTo).format("DD/MM/YYYY")}</td>
                    <td>{item.from_symbols}</td>
                    <td>{item.to_symbols}</td> */}
            {/* <td style={{fontWeight:600}}>{item.status == 1 && 'Connected'}</td> */}
          </tr>
        );
      })
    );
  };

  render() {
    let currentUser = JSON.parse(localStorage.getItem("currentUser"));
    return (
      <>
        <NotificationContainer />
        <Row>
              <div className="lastUpdate">
                <span> Last Updated : </span>
                {this.state.dateTime}(UTC){" "}
              </div>
            </Row>
        <div className="content">
          {this.state.detailForm ? (
            <FilterDetail
              data={this.state.detailData}
              show={this.state.detailForm}
              onHide={() => this.setState({ detailForm: false })}
            />
          ) : (
            <>
              {this.state.showEdit ? (
                <FilterEdite
                  data={this.state.editeData}
                  show={this.state.showEdit}
                  // onHide={() => this.setState({ showEdit: false })}
                  onHide={() =>
                    this.setState({ showEdit: false }, () =>
                      this.fetchfilterdata()
                    )
                  }
                />
              ) : (
                <>
                  {!this.state.Innerfilter && (
                    <Grid fluid>
                      <div className="selection-box">
                        <span className="terminal-text"> Profiles </span>

                        {/* <span className="icon-style">
                            <i className="fa fa-angle-down" aria-hidden="true"></i>
                        </span> */}
                        {/* <Link to="/innerfilter">
                       <button className="pull-right">Add New</button>
                       </Link> */}

                        {/* <Link to="/admin/innerfilter">
                       <button className="pull-right">abc</button>
                       </Link> */}
                      </div>
                      {currentUser[0].role !== "user" && (
                        <button
                          className="pull-right filter-add-button"
                          onClick={this.showInnerfilter}
                        >
                          Add New
                        </button>
                      )}
                    </Grid>
                  )}
                  {
                    this.state.Innerfilter ? (
                      <Innerfilter
                        show={this.state.Innerfilter}
                        onHide={() =>
                          this.setState({ Innerfilter: false }, () => {
                            this.fetchfilterdata();
                          })
                        }
                      />
                    ) : (
                      <div style={{ background: "#ffffff" }}>
                        <Row>
                          <Col md={12}>
                            <div style={{ padding: "10px" }}>
                              <table className="table table-striped table-bordered">
                                <thead>
                                  <tr>
                                    <th scope="col">Sr.No.</th>
                                    <th scope="col">Profile Name</th>
                                    <th scope="col">Created At</th>
                                    <th scope="col">Status</th>
                                    <th scope="col">Action</th>
                                  </tr>
                                </thead>
                                <tbody>
                                  {/* {this.state.accountlist.length > 0 && this.state.accountlist.map((item, index) => { */}

                                  {this.createTr()}
                                  {/* })} */}
                                  {/* <tr>
                                                <th scope="row">2</th>
                                                <td>Jacob</td>
                                                <td>Thornton</td>
                                                <td>5.1.2.9</td>
                                                <td>Connected</td>
                                            </tr>
                                            <tr>
                                                <th scope="row">3</th>
                                                <td>Larry</td>
                                                <td>the Bird</td>
                                                <td>ab@123</td>
                                                <td>Connected</td>
                                            </tr> */}
                                </tbody>
                              </table>
                              <ReactPaginate
                                previousLabel={"prev"}
                                nextLabel={"next"}
                                breakLabel={"..."}
                                pageCount={this.state.pages}
                                marginPagesDisplayed={9}
                                pageRangeDisplayed={9}
                                onPageChange={(e) => this.handlePageClick(e)}
                                containerClassName={"pagination"}
                                subContainerClassName={"pages pagination"}
                                activeClassName={"active"}
                              />
                            </div>
                          </Col>
                        </Row>
                      </div>
                    )
                    //     <div className="filter_page_table">
                    //     <table className="table table-striped">
                    //         <thead>
                    //             <tr>
                    //                 <th scope="col">Sr.No.</th>
                    //                 <th scope="col">Profile Name</th>
                    //                 <th scope="col">Created At</th>
                    //                 <th scope="col">Status</th>
                    //                 <th scope="col">Action</th>
                    //             </tr>
                    //         </thead>
                    //         <tbody>
                    //             {this.createTr()}
                    //         </tbody>
                    //     </table>

                    // </div>
                  }

                  {this.state.showDelete && (
                    <div className="delete_popup">
                      <h3>Are you sure ?</h3>
                      <span
                        className="pointer dltbtn"
                        onClick={(e) => this.deletepopupClick(e)}
                      >
                        Delete
                      </span>
                      <span
                        className="pointer closebtn"
                        onClick={(e) => this.deletepopupclose(e)}
                      >
                        Close
                      </span>
                    </div>
                  )}
                </>
              )}
            </>
          )}
        </div>
      </>
    );
  }
}

export default Filterprofile;
